// В этом файле должны быть стили для БЭМ-блока header, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.header {
	backdrop-filter: blur(.8rem);
	background-color: var(--back-primary_75p);
	color: var(--front-brand);
	fill: var(--front-brand);
	min-height: 7.5rem;
	padding: 2rem 0;
	transition: backdrop-filter .25s ease-in-out, background-image .25s ease-in-out;
	@media screen and (min-width: $screen-tl) {
		min-height: 18rem;
		padding-top: 4rem;
	}
	[data-state='nav_open'] & {
		backdrop-filter: blur(0);
		background-color: var(--back-brand);
	}
	@media screen and (min-width: $screen-d) {
		[data-state='nav_open'] & {
			background-color: var(--back-primary_75p);
			background-position: 0 100%;
		}
		[data-state='nav_open'] .headroom--not-top & {
			backdrop-filter: blur(.8rem);
			background-position: 0 0;
		}
	}
	&_mp {
		backdrop-filter: blur(0);
		background-color: transparent;
		background-image: var(--back-header-gradient);
		background-position: 0 100%;
		background-size: 100% 200%;
		color: var(--front-invert);
		fill: var(--front-invert);
		transition: background-position .25s ease, backdrop-filter .25s ease;
		[data-state='nav_open'] & {
			background-image: var(--back-header-gradient);
			background-position: 0 0;
		}
		.headroom--not-top & {
			backdrop-filter: blur(.8rem);
			background-position: 0 0;
		}
		@media screen and (min-width: $screen-d) {
			[data-state='nav_open'] & {
				background-image: var(--back-header-gradient);
				background-position: 0 100%;
			}
			[data-state='nav_open'] .headroom--not-top & {
				backdrop-filter: blur(.8rem);
				background-position: 0 0;
			}
		}
	}
	&__inner {
		display: grid;
		grid-template-areas:
			'logo menu';
		grid-template-columns: 10fr 2fr;
		@media screen and (min-width: $screen-t) {
			grid-template-columns: 4fr 8fr;
			grid-template-areas:
				'logo menu';
			margin: 0 -1rem;
		}
		@media screen and (min-width: $screen-ti) {
			grid-template-columns: 3fr 7fr 2fr;
			grid-template-areas:
				'logo nav menu';
		}
		@media screen and (min-width: $screen-d) {
			grid-template-columns: 3fr 5fr 4fr;
			grid-template-areas:
				'logo nav actions';
		}
		@media screen and (min-width: $screen-dxl) {
			grid-template-columns: 3fr 6fr 3fr;
		}
	}
	&__col {
		@media screen and (min-width: $screen-t) {
			padding: 0 1rem;
		}
		&:nth-child(1) {
			grid-area: logo;
		}
		&:nth-child(2) {
			display: none;
			grid-area: nav;
			@media screen and (min-width: $screen-ti) {
				display: block;
				padding-top: .5rem;
			}
		}
		&:nth-child(3) {
			align-items: flex-start;
			display: none;
			grid-area: actions;
			flex-direction: column;
			@media screen and (min-width: $screen-d) {
				align-items: flex-end;
				display: flex;
			}
		}
		&:nth-child(4) {
			display: flex;
			justify-content: flex-end;
			grid-area: menu;
			@media screen and (min-width: $screen-d) {
				display: none;
			}
		}
	}
	&__logo {
		display: block;
		height: 12rem;
		width: 11.5rem;
		&[href] {
			transition: opacity .25s ease;
			&:hover {
				opacity: .75;
			}
		}
	}
	&__logo-img {
		fill: inherit;
		height: 100%;
		transition: fill .25s ease-in-out;
		width: 100%;
		//.headroom--not-top &,
		[data-state='nav_open'] & {
			fill: var(--front-invert);
		}
		.headroom--not-top .header_mp &,
		[data-state='nav_open'] .header_mp & {
			fill: inherit;
		}
		@media screen and (min-width: $screen-d) {
			.headroom--not-top &,
			[data-state='nav_open'] & {
				fill: var(--front-brand);
			}
			.headroom--not-top .header_mp &,
			[data-state='nav_open'] .header_mp & {
				fill: inherit;
			}
		}
	}
	&__nav {
		max-width: 25rem;
		@media screen and (min-width: $screen-d) {
			max-width: 35rem;
		}
	}
	&__list {
		display: grid;
		gap: 1rem 5rem;
		grid-template-columns: 1fr 1fr;
		grid-auto-flow: row dense;
		list-style: none;
		margin: 0;
		padding: 0;
		@media screen and (min-width: $screen-tl) {
			gap: 1.5rem 5rem;
		}
	}
	&__item {}
	&__link {
		color: inherit;
		@include style_1\8_1\12(400);
		text-decoration: none;
		transition: opacity .25s ease;
		white-space: nowrap;
		[data-state='nav_open'] & {
			color: var(--front-invert);
		}
		@media screen and (min-width: $screen-d) {
			[data-state='nav_open'] & {
				color: inherit;
			}
		}
		&:hover {
			opacity: .75;
		}
	}
	&__actions {
		display: flex;
		flex-direction: column;
		gap: 2rem;
		width: 100%;
		@media screen and (min-width: $screen-d) {
			width: 38.5rem;
		}
	}
	&__row {
		display: flex;
		gap: 2rem;
		&:nth-child(1) > *:nth-child(1) {
			flex: 1 0 auto;
		}
		&:nth-child(1) > *:nth-child(2) {
			width: 21rem;
		}
		&:nth-child(2) > *:nth-child(1){
			flex: 1 0 auto;
		}
		&:nth-child(2) > *:nth-child(2) {
			width: 10rem;
		}
	}
	.header_mp &__join {
		outline: .1rem solid var(--border-light_50p);
	}
	&__menu {
		align-self: start;
		cursor: pointer;
		justify-self: end;
		height: 4.2rem;
		outline: .1rem solid var(--border-dark);
		padding: 1rem;
		transition: outline-offset .25s ease-in-out;
		width: 4.2rem;
		.header_mp &,
		[data-state='nav_open'] & {
			outline: .1rem solid var(--border-light_50p);
		}
		&:active {
			outline-offset: -.5rem;
		}
	}
	&__icon {
		fill: var(--front-brand);
		height: 100%;
		stroke: var(--front-brand);
		transition: fill .25s ease-in-out, stroke .25s ease-in-out;
		width: 100%;
		.header_mp &,
		.headroom--not-top .header_mp &,
		[data-state='nav_open'] & {
			fill: var(--front-invert);
			stroke: var(--front-invert);
		}
	}
	&__icon-dot {
		fill: inherit;
		opacity: 1;
		transition: opacity .25s ease-in-out;
		[data-state='nav_open'] & {
			opacity: 0;
		}
	}
	&__icon-line {
		stroke: inherit;
		stroke-width: .2rem;
		transform: scale(0);
		transform-origin: center center;
		transition: transform .25s ease-in-out;
		[data-state='nav_open'] & {
			transform: scale(1);
		}
	}
}
