// В этом файле должны быть стили для БЭМ-блока named-block, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.kv-block {
	display: grid;
	@media screen and (min-width: $screen-t) {
		gap: .5rem;
	}
	&__title {
		color: var(--front-primary_50p);
		@include style_1\2_1\25;
		text-transform: uppercase;
	}
	&__body {
		display: grid;
		gap: .5rem;
	}
	&__descr {
		@include style_1\5_1\33;
	}
	&__link {
		color: var(--front-brand);
		@include style_1\5_1\33;
		text-decoration: none;
		&:hover {
			text-decoration: underline;
		}
	}
}
