// В этом файле должны быть стили для БЭМ-блока inline-filter, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.inline-filter {
	display: flex;
	flex-wrap: wrap;
	gap: .5rem;
	width: 100%;
	@media screen and (min-width: $screen-t) {
		gap: 1rem;
	}
	&__item {
		align-items: center;
		border: .1rem solid var(--border-brand-light);
		display: flex;
		min-height: 3.5rem;
		padding: .4rem .4rem .4rem 1.4rem;
		gap: 5px;
		overflow: hidden;
		user-select: none;
		width: 100%;
		@media screen and (min-width: $screen-t) {
			width: auto;
		}
		&:first-of-type {
			background: var(--back-tertiary);
			cursor: pointer;
			padding: .4rem 1.4rem;
			transition: border .25s ease;
			&:hover {
				border: .1rem solid var(--border-brand);
			}
		}
	}
	&__inner {
		display: flex;
		flex: 1;
		flex-direction: column;
		gap: 5px;
		@media screen and (min-width: $screen-t) {
			flex-direction: row;
		}
	}
	&__title {
		@include style_1\5_1(700);
		text-align: center;
		width: 100%;
	}
	&__key {
		color: var(--front-primary_50p);
		@include style_1\5_1;
		flex-shrink: 0;
	}
	&__value {
		@include style_1\5_1;
	}
	&__icon {
		cursor: pointer;
		fill: var(--front-brand);
		flex-shrink: 0;
		height: 2.5rem;
		width: 2.5rem;
	}
}
