// В этом файле должны быть стили для БЭМ-блока school-tile, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.school-tile {
	background: var(--back-primary);
	border: .1rem solid var(--border-brand-light);
	color: var(--front-primary);
	display: flex;
	gap: 1.5rem;
	padding: 1.9rem;
	text-decoration: none;
	transition: border .25s ease, color .25s ease, transform .25s ease;
	&:hover {
		border: .1rem solid var(--border-brand);
		color: var(--front-brand);
		transform: translateY(-.5rem);
	}
	&__logo {
		display: none;
		height: 7.5rem;
		flex: 0 0 auto;
		width: 7.5rem;
		@media screen and (min-width: $screen-t) {
			display: block;
		}
	}
	&__inner {
		display: flex;
		flex-direction: column;
	}
	&__region {
		color: var(--front-brand);
		@include style_1\2_1\25;
		text-transform: uppercase;
	}
	&__title {
		color: var(--front-primary);
		@include style_1\8_1\12(700);
		margin: .5rem 0 1.5rem;
	}
	&__status {
		align-items: center;
		color: var(--front-primary);
		display: flex;
		@include style_1\2_1\25;
		gap: 1rem;
		margin-top: auto;
		text-transform: uppercase;
	}
	&__icon {
		height: 2.5rem;
		flex: 0 0 auto;
		stroke: var(--front-brand);
		stroke-width: .15rem;
		width: 2.5rem;
	}
}
