// В этом файле должны быть стили для БЭМ-блока input-file, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.input-file {
	display: block;
	width: 100%;
	&__fake-input {
		border: .1rem solid var(--border-brand_50p);
		color: var(--front-primary_50p);
		cursor: pointer;
		display: flex;
		justify-content: center;
		height: 4.5rem;
		padding: 1.4rem 1.9rem;
		transition: border .25s ease-out;
		width: 100%;
	}
	&_has-value &__fake-input {
		border: .1rem solid var(--border-brand);
		color: var(--front-primary);
	}
	&__title {
		@include input;
		max-width: 29rem;
		overflow: hidden;
		text-align: center;
		text-overflow: ellipsis;
		user-select: none;
		white-space: nowrap;
	}
	input[type="file"] {
		display: none;
	}
	input[type="file"][disabled] + &__fake-input {
		background: var(--back-tertiary);
		border: .1rem solid var(--border-brand-light);
		color: var(--front-primary_25p);
		cursor: not-allowed;
	}
}
