// В этом файле должны быть стили для БЭМ-блока section, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.section {
	padding: 2rem 0 5rem;
	@media screen and (min-width: $screen-ti) {
		padding: 5rem 0 7rem;
		&:first-child {
			padding-top: 9rem;
		}
		&:last-child {
			padding-bottom: 10rem;
		}
	}
	&__grid {
		width: 100%;
		@media screen and (min-width: $screen-tl) {
			display: flex;
			margin: 0 -1rem;
			width: calc(100% + 2rem);
		}
	}
	&__col {
		flex: 0 0 auto;
		@media screen and (min-width: $screen-tl) {
			padding: 0 1rem;
			width: 100%;
			&_offset_1 {
				margin-left: 8.3333%;
			}
			&_1 {
				width: 8.3333%;
			}
			&_2 {
				width: 16.6667%;
			}
			&_3 {
				width: 25%;
			}
			&_5 {
				width: 41.6667%;
			}
			&_7 {
				width: 58.3334%;
			}
			&_9 {
				width: 75%;
			}
		}
		@media screen and (min-width: $screen-d) {
			&_offset_1_d {
				margin-left: 8.3333%;
			}
			&_2_d {
				width: 16.6667%;
			}
			&_8_d {
				width: 66.6667%;
			}
		}
		@media screen and (min-width: $screen-dl) {
			&_3_dl {
				width: 25%;
			}
			&_6_dl {
				width: 50%;
			}
			&_8_dl {
				width: 66.6667%;
			}
		}
		@media screen and (min-width: $screen-dxl) {
			&_5_dxl {
				width: 41.6667%;
			}
			&_6_dxl {
				width: 50%;
			}
		}
	}
	&__col-title {
		color: var(--front-primary_50p);
		@include style_1\8_1\12;
		margin: 0 0 1.5rem;
	}
	&__block {
		& + & {
			margin-top: 5rem;
			@media screen and (min-width: $screen-t) {
				margin-top: 10rem;
			}
			@media screen and (min-width: $screen-tl) {
				margin-top: 16rem;
			}
		}
	}
	&__title {
		@include section-title;
		margin: 1.5rem 0 3.5rem;
		position: relative;
		text-transform: uppercase;
		@media screen and (min-width: $screen-tl) {
			margin: 1.5rem 0 10rem;
		}
		&::before {
			color: var(--front-primary_50p);
			content: attr(data-subtitle);
			@include section-subtitle;
			position: absolute;
			top: -1.5rem;
		}
		span {
			color: var(--front-brand);
		}
		br {
			display: none;
			@media screen and (min-width: $screen-tl) {
				display: block;
			}
		}
	}
	&__subtitle {
		color: var(--front-primary_50p);
		@include grid-subtitle;
		margin-bottom: 1.5rem;
		@media screen and (min-width: $screen-tl) {
			margin: 0;
		}
	}
	&__descr {
		@include person-title;
		margin: 0 0 2rem;
	}
}
