// В этом файле должны быть стили для БЭМ-блока region-info, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.region-info {
	@media screen and (min-width: $screen-ti) {
		width: 50%;
	}
	@media screen and (min-width: $screen-tl) {
		left: 50%;
		position: absolute;
		top: 0;
		transition: transform .5s ease-in-out;
		width: 30rem;
		will-change: transform;
		[data-region='moscow'] & {
			transform: translate(-138%, 0);
		}
		[data-region='south'] & {
			transform: translate(-135%, 10rem);
		}
		[data-region='volga'] & {
			transform: translate(-103%, 2rem);
		}
		[data-region='ural'] & {
			transform: translate(-55%, 10rem);
		}
		[data-region='siberia'] & {
			transform: translate(50%, 7rem);
		}
		[data-region='center'] & {
			transform: translate(-115%, 0);
		}
		[data-region='baltic'] & {
			transform: translate(-55%, 11rem);
		}
		[data-region='baikal'] & {
			transform: translate(-75%, 2rem);
		}
		[data-region='bashkortostan'] & {
			transform: translate(-85%, 9rem);
		}
	}
	@media screen and (min-width: $screen-d) {
		width: 42rem;
		[data-region='moscow'] & {
			transform: translate(-125%, 8rem);
		}
		[data-region='south'] & {
			transform: translate(-130%, 20rem);
		}
		[data-region='volga'] & {
			transform: translate(-93%, 10rem);
		}
		[data-region='ural'] & {
			transform: translate(-50%, 21rem);
		}
		[data-region='siberia'] & {
			transform: translate(45%, 15rem);
		}
		[data-region='center'] & {
			transform: translate(-110%, 5rem);
		}
		[data-region='baltic'] & {
			transform: translate(-50%, 21rem);
		}
		[data-region='baikal'] & {
			transform: translate(-66%, 16rem);
		}
		[data-region='bashkortostan'] & {
			transform: translate(-85%, 19rem);
		}
	}
	@media screen and (min-width: $screen-dl) {
		[data-region='moscow'] & {
			transform: translate(-133%, 3rem);
		}
		[data-region='south'] & {
			transform: translate(-135%, 18rem);
		}
		[data-region='volga'] & {
			transform: translate(-100%, 6rem);
		}
		[data-region='ural'] & {
			transform: translate(-55%, 16rem);
		}
		[data-region='siberia'] & {
			transform: translate(50%, 12rem);
		}
		[data-region='center'] & {
			transform: translate(-115%, 2rem);
		}
		[data-region='baltic'] & {
			transform: translate(-55%, 17rem);
		}
		[data-region='baikal'] & {
			transform: translate(-64%, 12rem);
		}
		[data-region='bashkortostan'] & {
			transform: translate(-95%, 15rem);
		}
	}
	@media screen and (min-width: $screen-dxl) {
		[data-region='moscow'] & {
			transform: translate(-160%, 3rem);
		}
		[data-region='south'] & {
			transform: translate(-165%, 18rem);
		}
		[data-region='volga'] & {
			transform: translate(-125%, 4rem);
		}
		[data-region='ural'] & {
			transform: translate(-65%, 17rem);
		}
		[data-region='siberia'] & {
			transform: translate(63%, 12rem);
		}
		[data-region='center'] & {
			transform: translate(-135%, 1rem);
		}
		[data-region='baltic'] & {
			transform: translate(-65%, 17rem);
		}
		[data-region='baikal'] & {
			transform: translate(-65%, 8rem);
		}
		[data-region='bashkortostan'] & {
			transform: translate(-115%, 15rem);
		}
	}
	&__inner {
		align-items: flex-start;
		display: flex;
		gap: 1.5rem;
		@media screen and (min-width: $screen-d) {
			gap: 2rem;
		}
	}
	&__img {
		border-radius: 50%;
		display: block;
		height: 8rem;
		flex: 0 0 auto;
		width: 8rem;
		@media screen and (min-width: $screen-d) {
			height: 11.5rem;
			width: 11.5rem;
		}
	}
	&__col {
		display: grid;
		flex: 1 1 auto;
		gap: 2rem;
		padding-top: 1rem;
		@media screen and (min-width: $screen-d) {
			padding-top: 2.5rem;
		}
	}
	&__item {
		align-items: flex-start;
		display: flex;
		flex-direction: column;
	}
	&__item-title {
		color: var(--front-primary_50p);
		@include section-subtitle;
		margin-bottom: .5rem;
		text-indent: .2rem;
		text-transform: uppercase;
	}
	&__title {
		color: var(--front-brand);
		@include region-title;
		text-decoration: none;
		transition: opacity .25s ease;
		&:hover {
			opacity: .6;
		}
	}
	&__item:first-child &__item-title {
		margin: 0;
	}
	&__link {
		color: inherit;
		@include region-coordinator;
		text-decoration: none;
		transition: opacity .25s ease;
		&:hover {
			opacity: .6;
		}
		span {
			color: var(--front-brand);
			display: block;
			font-weight: 700;
		}
	}
	&__data {
		@include style_1\5_1;
		white-space: nowrap;
		span {
			color: var(--front-brand);
			font-weight: 700;
		}
		& + & {
			margin-top: .5rem;
		}
	}
}
