// В этом файле должны быть стили для БЭМ-блока news-tile-, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.news-tile {
	background: var(--back-primary);
	border: .1rem solid var(--border-brand-light);
	display: flex;
	flex-direction: column;
	height: 100%;
	max-height: 26rem;
	overflow: hidden;
	padding: 1.5rem;
	@media screen and (min-width: $screen-t) {
		padding: 2rem;
	}
	&__info {
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		margin-bottom: .5rem;
		@media screen and (min-width: $screen-t) {
			margin-bottom: 1rem;
		}
	}
	&__data {
		margin-right: 1rem;
	}
	&__row {
		align-items: flex-end;
		display: flex;
		justify-content: space-between;
		margin: auto -2.5rem 0;
	}
	&__col {
		padding: 0 1rem;
		&:nth-child(1) {
			padding-left: 2.5rem;
			width: 20%;
			@media screen and (min-width: $screen-t) {
				padding-left: 3rem;
				width: 25%;
			}
		}
		&:nth-child(2) {
			padding-right: 2.5rem;
			width: 80%;
			@media screen and (min-width: $screen-t) {
				padding-right: 3rem;
				width: 75%;
			}
		}
	}
	&__icon {
		height: 2rem;
		width: 2rem;
	}
	&__region {
		color: var(--front-primary_50p);
		@include style_1\2_1\25;
		text-decoration: none;
		text-transform: uppercase;
		transition: opacity .25s ease;
		white-space: nowrap;
		&:hover {
			opacity: .8;
		}
	}
	&__link {
		color: inherit;
		text-decoration: none;
		transition: color .25s ease;
		&:hover {
			color: var(--front-brand);
		}
	}
	&__title {
		@include news-tile-title;
		margin: 0;
		text-transform: uppercase;
	}
	&__descr {
		@include  news-tile-descr;
	}
}
