// В этом файле должны быть стили для БЭМ-блока school-status, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.school-status {
	display: flex;
	flex-wrap: wrap;
	gap: 1rem;
	@media screen and (min-width: $screen-ti) {
		gap: 2.5rem;
	}
	&__status {
		align-items: center;
		border: .1rem dashed var(--border-dark_50p);
		border-radius: .2rem;
		color: var(--front-brand);
		display: flex;
		@include style_1\5_1\33(700);
		gap: 1.5rem;
		padding: 1rem 2rem 1rem 1.5rem;
		text-transform: uppercase;
	}
	&__icon {
		flex: 0 0 auto;
		height: 2.5rem;
		width: 2.5rem;
		:is(.school-status__status) & {
			stroke: var(--front-brand);
			stroke-width: .2rem;
		}
	}
	&__link {
		align-items: center;
		border: .1rem solid var(--border-brand-light);
		border-radius: .2rem;
		color: inherit;
		display: flex;
		@include style_1\5_1\33;
		gap: 1rem;
		padding: 1rem 2rem 1rem 1.5rem;
		text-decoration: none;
		transition: border .25s ease;
		&:hover {
			border: .1rem solid var(--border-brand);
		}
	}
}
