// В этом файле должны быть стили для БЭМ-блока hero-back-slider, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.hero-back-slider {
	&::before {
		background: linear-gradient(0deg, rgba(0, 105, 180, 0.5), rgba(0, 105, 180, 0.5));
		bottom: 0;
		content: '';
		display: block;
		left: 0;
		position: absolute;
		right: 0;
		top: 0;
		z-index: 2;
	}
	&__inner {}
	&__slide {
		align-items: center;
		display: flex;
		justify-content: center;
		position: relative;
	}
	&__box {
		height: 100%;
		width: 100%;
	}
	&__img {
		display: block;
		filter: blur(2.5rem);
		height: 100%;
		object-fit: cover;
		position: absolute;
		width: 100%;
		transform: scale(1.15);
	}
}
