// В этом файле должны быть стили для БЭМ-блока input, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.input {
	appearance: none;
	border: .1rem solid var(--border-brand_50p);
	@include input;
	height: 4.5rem;
	outline: none;
	padding: 1.4rem 1.9rem;
	transition: border .25s ease-out;
	&::placeholder {
		color: var(--front-primary_50p);
	}
	&_datePicker {
		padding: 1.4rem .9rem;
		text-align: center;
	}
	&_has-value,
	&:focus {
		border: .1rem solid var(--border-brand);
	}
	&[disabled] {
		background: var(--back-tertiary);
		border: .1rem solid var(--border-brand-light);
		cursor: not-allowed;
		&::placeholder {
			color: var(--front-primary_25p);
		}
	}
	&[type='number'],
	&[type='number']::-webkit-inner-spin-button,
	&[type='number']::-webkit-outer-spin-button {
   -moz-appearance: textfield;
-webkit-appearance: none;
		margin: 0;
	}
}
