// В этом файле должны быть стили для БЭМ-блока select, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.select {
	appearance: none;
	background: url('../img/svgSprite.svg#icon__arrow_down-view') right 1rem top 1rem no-repeat, var(--back-primary);
	background-size: 2.5rem 2.5rem;
	border: .1rem solid var(--border-brand_50p);
	display: block;
	@include input;
	height: 4.5rem;
	outline: none;
	padding: 1.4rem 1.9rem;
	transition: border .25s ease-out;
	width: 100%;
	&::placeholder {
		color: var(--front-primary_50p);
	}
	&_has-value,
	&:focus {
		border: .1rem solid var(--border-brand);
	}
}
