// В этом файле должны быть стили для БЭМ-блока last-news, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.last-news {
	position: relative;
	z-index: 2;
	@media screen and (min-width: $screen-tl) {
		.section__col:nth-child(1) {
			display: flex;
			flex-direction: column;
		}
	}
	.coordinator + &,
	.school + & {
		background: var(--back-tertiary);
	}
	&__inner {}
	&__actions {
		display: flex;
		flex-direction: column;
		gap: 2.5rem;
		margin-top: auto;
		&_mobile {
			flex-direction: column-reverse;
			margin-top: 1.5rem;
			@media screen and (min-width: $screen-ti) {
				flex-direction: row;
				justify-content: space-between;
			}
			@media screen and (min-width: $screen-tl) {
				display: none;
			}
		}
		&_desktop {
			display: none;
			@media screen and (min-width: $screen-tl) {
				display: flex;
			}
		}
	}
	&__subscribe {
		align-items: center;
		color: var(--front-brand);
		cursor: pointer;
		display: flex;
		gap: 1rem;
		@media screen and (min-width: $screen-d) {
			max-width: 21rem;
		}
	}
	&__subscribe-icon {
		flex: 0 0 auto;
		height: 3rem;
		stroke: var(--front-brand);
		stroke-width: .1rem;
		width: 3rem;
	}
	&__row {
		display: flex;
		gap: 2rem;
		@media screen and (min-width: $screen-tl) {
			align-items: flex-start;
			flex-direction: column;
			gap: 1rem;
		}
		@media screen and (min-width: $screen-d) {
			flex-direction: row;
			gap: 2rem;
		}
	}
	&__button {
		color: var(--front-primary);
		padding: .9rem 1.9rem;
	}
	&__slider {
		height: 25rem;
		width: 100%;
		@media screen and (min-width: $screen-ti) {
			height: 54rem;
		}
		@media screen and (min-width: $screen-tl) {
			padding-right: 4rem;
			width: calc(100% + 4rem);
		}
		@media screen and (min-width: $screen-dxl) {
			padding-right: calc((100vw - 160rem) / 2);
			width: calc(100% + ((100vw - 160rem) / 2));
		}
	}
	&__slider-inner {}
	&__slide {
		@media screen and (min-width: $screen-ti) {
			height: calc((100% - 2rem) / 2);
			width: calc(((100vw - 8rem) / 2) - 1rem);
		}
		@media screen and (min-width: $screen-tl) {
			width: calc((100vw - 8rem) / 2);
		}
		@media screen and (min-width: $screen-d) {
			max-width: 52rem;
			width: calc((100vw - 8rem) / 3);
		}
	}
	&__next,
	&__prev {
		padding: .4rem;
	}
	&__icon {
		height: 2.5rem;
		stroke: var(--front-primary);
		width: 2.5rem;
	}
}
