// В этом файле должны быть стили для БЭМ-блока widget, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.widget {
	position: relative;
	z-index: 2;
	@media screen and (min-width: $screen-tl) {
		.section__col:nth-child(1) {
			display: flex;
			flex-direction: column;
		}
	}
	&__inner {}
	&__actions {
		display: flex;
		flex-direction: column;
		gap: 2.5rem;
		margin-top: auto;
		&_mobile {
			flex-direction: column-reverse;
			margin-top: 1.5rem;
			@media screen and (min-width: $screen-ti) {
				flex-direction: row;
				justify-content: space-between;
			}
			@media screen and (min-width: $screen-tl) {
				display: none;
			}
		}
		&_desktop {
			display: none;
			@media screen and (min-width: $screen-tl) {
				display: flex;
			}
		}
	}
	&__row {
		display: flex;
		gap: 2rem;
		@media screen and (min-width: $screen-tl) {
			align-items: flex-start;
			flex-direction: column;
			gap: 1rem;
		}
		@media screen and (min-width: $screen-d) {
			flex-direction: row;
			gap: 2rem;
		}
	}
	&__button {
		color: var(--front-primary);
		padding: .9rem 1.9rem;
	}
}
