// В этом файле должны быть стили для БЭМ-блока filter, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.filter {
	display: grid;
	gap: 1rem;
	margin-bottom: 2rem;
	@media screen and (min-width: $screen-tl) {
		max-width: 35rem;
	}
	&__trigger {
		border: 1px solid var(--border-brand-light);
		color: var(--front-primary_50p);
		cursor: pointer;
		@include style_1\2_1\25;
		padding: .9rem;
		text-align: center;
		text-transform: uppercase;
		transition: border .25s ease, color .25s ease;
		&_active {
			border: 1px solid var(--border-brand);
			color: var(--front-primary);
		}
		&:hover {
			border: 1px solid var(--border-brand);
		}
		@media screen and (min-width: $screen-tl) {
			display: none;
		}
	}
	&__body {
		border: 1px solid var(--border-brand-light);
		display: none;
		@media screen and (min-width: $screen-tl) {
			display: block;
		}
		&_visible {
			display: block;
		}
	}
	&__form {
		display: grid;
		gap: 3.5rem;
		padding: 1.9rem;
		@media screen and (min-width: $screen-d) {
			padding: 2.9rem;
		}
	}
	&__inner {
		display: grid;
		gap: 3rem;
		@media screen and (min-width: $screen-t) {
			grid-template-columns: 1fr 1fr;
		}
		@media screen and (min-width: $screen-tl) {
			grid-template-columns: 1fr;
		}
	}
	&__section {}
	&__label {
		color: var(--front-primary_50p);
		@include style_1\2_1\25;
		margin-bottom: 1rem;
		text-transform: uppercase;
	}
	&__block {
		display: grid;
		gap: 1rem;
		.checkbox {
			justify-self: start;
		}
	}
	&__row {
		display: flex;
		flex-direction: column;
		position: relative;
		& > * {
			flex: 1 0 auto;
		}
	}
	&__footer {
		display: grid;
		gap: 1rem;
		@media screen and (min-width: $screen-t) {
			grid-template-columns: 1fr 1fr;
		}
		@media screen and (min-width: $screen-tl) {
			grid-template-columns: 1fr;
		}
		@media screen and (min-width: $screen-d) {
			grid-template-columns: 1fr 1fr;
		}
	}
}
