// В этом файле должны быть стили для БЭМ-блока coordinator, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.coordinator {
	&__inner {
		display: grid;
		gap: 3rem 2rem;
		grid-template-areas:
			'avatar'
			'info'
			'tags';
		@media screen and (min-width: $screen-t) {
			gap: 5rem 2rem;
			grid-template-areas:
				'avatar info'
				'avatar tags';
			grid-template-columns: 3fr 9fr;
		}
		@media screen and (min-width: $screen-d) {
			grid-template-areas:
				'avatar info tags';
			grid-template-columns: 3fr 6fr 3fr;
		}
	}
	&__avatar {
		display: block;
		grid-area: avatar;
		height: auto;
		max-width: calc(60% - 2rem);
		@media screen and (min-width: $screen-t) {
			max-width: 100%;
		}
		@media screen and (min-width: $screen-d) {
			justify-self: end;
			max-width: 25rem;
		}
	}
	&__info {
		grid-area: info;
		max-width: 65.5rem;
	}
	&__label {
		color: var(--front-primary_50p);
		@include style_1\2_1\25;
		text-transform: uppercase;
	}
	&__title {
		@include section-title(400);
		margin-bottom: 1.5rem;
		span {
			color: var(--front-brand);
			font-weight: 700;
		}
	}
	&__description {
		color: var(--front-primary_75p);
		@include style_1\5_1\33;
		margin-bottom: 3rem;
	}
	:is(.coordinator) &__contacts {
		padding: 1.9rem;
		@media screen and (min-width: $screen-t) {
			padding: 1.9rem;
		}
		@media screen and (min-width: $screen-tl) {
			padding-left: 11.9rem;
		}
	}
	&__tags {
		grid-area: tags;
		@media screen and (min-width: $screen-d) {
			.tags__inner {
				align-items: flex-start;
				flex-direction: column;
			}
		}
		@media screen and (min-width: $screen-dl) {
			max-width: 25rem;
		}
	}
}
