// В этом файле должны быть стили для БЭМ-блока hero, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.hero {
	background: var(--back-brand);
	height: 100%;
	padding-bottom: 10rem;
	padding-top: 16rem;
	position: relative;
	z-index: 2;
	@media screen and (min-width: $screen-tl) {
		max-height: 80rem;
		min-height: 65.6rem;
		padding-bottom: 0;
		padding-top: 18rem;
	}
	@media screen and (min-width: $screen-d) {
		height: 100vh;
		min-height: 60rem;
	}
	.grid {
		height: 100%;
		position: relative;
		z-index: 2;
	}
	&__inner {
		align-items: center;
		display: grid;
		gap: 10rem;
		height: 100%;
		@media screen and (min-width: $screen-tl) {
			gap: 0;
			grid-template-columns: 9fr 3fr;
			margin: 0 -1rem;
		}
	}
	&__col {
		@media screen and (min-width: $screen-tl) {
			align-items: center;
			display: flex;
			position: relative;
			padding: 0 1rem;
			&:nth-child(1) {
				padding: 0;
			}
		}
	}
	&__back-slider {
		height: 100%;
		left: 0;
		pointer-events: none;
		position: absolute;
		top: 0;
		width: 100%;
	}
}
