// В этом файле должны быть стили для БЭМ-блока map, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.map {
	height: 62rem;
	position: relative;
	@media screen and (min-width: $screen-t) {
		height: auto;
	}
	@media screen and (min-width: $screen-tl) {
		height: 70rem;
	}
	@media screen and (min-width: $screen-d) {
		height: 82.5rem;
	}
	.grid {
		height: 100%;
	}
	.section__grid {
		@media screen and (min-width: $screen-d) {
			width: calc(100% + 2rem);
		}
	}
	.section__col_9 {
		position: relative;
	}
	&__inner {
		height: 100%;
		position: relative;
		z-index: 2;
		@media screen and (min-width: $screen-tl) {
			align-items: flex-end;
			display: flex;
			flex-direction: column;
		}
	}
	&__image {
		height: auto;
		max-width: 168rem;
		pointer-events: none;
		position: absolute;
		top: 0;
		left: 0;
		transition: transform .5s ease-in-out;
		width: 92rem;
		will-change: transform;
		z-index: 1;
		[data-region='moscow'] & {
			transform: translate3d(73rem, 0, 0) scale(3);
		}
		[data-region='south'] & {
			transform: translate3d(41rem, -17rem, 0) scale(1.75);
		}
		[data-region='volga'] & {
			transform: translate3d(18rem, -4rem, 0) scale(1.5);
		}
		[data-region='ural'] & {
			transform: translate3d(-6rem, 4rem, 0) scale(1);
		}
		[data-region='siberia'] & {
			transform: translate3d(-27rem, 0, 0) scale(1);
		}
		[data-region='center'] & {
			transform: translate3d(41rem, 1rem, 0) scale(2);
		}
		[data-region='baltic'] & {
			transform: translate3d(3rem, 17rem, 0) scale(1.25);
		}
		[data-region='baikal'] & {
			transform: translate3d(-46rem, 7rem, 0) scale(.8);
		}
		[data-region='bashkortostan'] & {
			transform: translate3d(45rem, -28rem, 0) scale(3);
		}
		[data-region='moscow'] & .moscow,
		[data-region='moscow'] & .moscow path,
		[data-region='south'] & .south,
		[data-region='south'] & .south path,
		[data-region='volga'] & .volga,
		[data-region='volga'] & .volga path,
		[data-region='ural'] & .ural,
		[data-region='ural'] & .ural path,
		[data-region='siberia'] & .siberia,
		[data-region='siberia'] & .siberia path,
		[data-region='center'] & .center,
		[data-region='center'] & .center path,
		[data-region='baltic'] & .baltic,
		[data-region='baltic'] & .baltic path,
		[data-region='baikal'] & .baikal,
		[data-region='baikal'] & .baikal path,
		[data-region='bashkortostan'] & .bashkortostan {
			fill: var(--back-secondary);
		}
		@media screen and (min-width: $screen-t) {
			top: -10rem;
		}
		@media screen and (min-width: $screen-ti) {
			top: -5rem;
		}
		@media screen and (min-width: $screen-tl) {
			left: 50%;
			top: 0;
			//width: 100vw;
			transition: none;
			width: 102.4rem;
			[data-region='moscow'] &,
			[data-region='south'] &,
			[data-region='volga'] &,
			[data-region='ural'] &,
			[data-region='siberia'] &,
			[data-region='center'] &,
			[data-region='baltic'] &,
			[data-region='baikal'] &,
			[data-region='bashkortostan'] & {
				transform: translate(-50%, -3rem);
			}
		}
		@media screen and (min-width: $screen-d) {
			width: 128rem;
			[data-region='moscow'] &,
			[data-region='south'] &,
			[data-region='volga'] &,
			[data-region='ural'] &,
			[data-region='siberia'] &,
			[data-region='center'] &,
			[data-region='baltic'] &,
			[data-region='baikal'] &,
			[data-region='bashkortostan'] & {
				transform: translate(-50%, 0);
			}
		}
		@media screen and (min-width: $screen-dl) {
			width: 144rem;
			[data-region='moscow'] &,
			[data-region='south'] &,
			[data-region='volga'] &,
			[data-region='ural'] &,
			[data-region='siberia'] &,
			[data-region='center'] &,
			[data-region='baltic'] &,
			[data-region='baikal'] &,
			[data-region='bashkortostan'] & {
				transform: translate(-50%, -10rem);
			}
		}
		@media screen and (min-width: $screen-dxl) {
			width: 168rem;
			[data-region='moscow'] &,
			[data-region='south'] &,
			[data-region='volga'] &,
			[data-region='ural'] &,
			[data-region='siberia'] &,
			[data-region='center'] &,
			[data-region='baltic'] &,
			[data-region='baikal'] &,
			[data-region='bashkortostan'] & {
				transform: translate(-50%, -18rem);
			}
		}
		path {
			fill: var(--back-tertiary);
			transition: fill .5s ease .5s;
			@media screen and (min-width: $screen-tl) {
				transition: fill .5s ease;
			}
		}
	}
	&__title {
		@media screen and (min-width: $screen-tl) {
			text-align: right;
		}
		:is(.map) & {
			margin-bottom: 1.5rem;
		}
		&::before {
			@media screen and (min-width: $screen-tl) {
				padding-left: .8rem;
			}
		}
	}
	&__info {
		@include map-info;
		@media screen and (min-width: $screen-ti) {
			margin-bottom: -5.4rem;
			margin-top: .1rem;
			max-width: 30rem;
		}
		@media screen and (min-width: $screen-tl) {
			text-align: right;
		}
		span {
			color: var(--front-brand);
			font-weight: 700;
			white-space: nowrap;
		}
	}
	&__slider {
		height: 100%;
		padding-top: 1.5rem;
		width: 100%;
		@media screen and (min-width: $screen-tl) {
			padding: 0;
		}
	}
	&__slider-pagination {
		display: none;
		justify-content: space-between;
		padding-right: 10rem;
		position: relative;
		@media screen and (min-width: $screen-d) {
			display: flex;
		}
		@media screen and (min-width: $screen-dl) {
			padding-right: 11rem;
		}
		@media screen and (min-width: $screen-dxl) {
			padding-right: 13.5rem;
		}
		&::after {
			background: var(--front-brand_50p);
			bottom: .1rem;
			content: '';
			display: block;
			height: .1rem;
			left: 0;
			position: absolute;
			width: calc(100% + 15rem);
			@media screen and (min-width: $screen-dxl) {
				width: calc(100% + calc((100vw - 160rem) / 2));
			}
		}
	}
	&__main-slider {
		overflow: visible;
	}
	&__main-slide {
		height: 25.1rem;
		pointer-events: none;
		position: relative;
		@media screen and (min-width: $screen-t) {
			display: flex;
			justify-content: flex-end;
		}
		@media screen and (min-width: $screen-ti) {
			height: 28rem;
		}
		@media screen and (min-width: $screen-tl) {
			display: block;
			height: 34rem;
		}
		@media screen and (min-width: $screen-d) {
			height: 46.5rem;
		}
		&.swiper-slide-active {
			pointer-events: auto;
		}
	}
	&__nav-slider {
		margin: 0 -1.5rem;
		padding: 0 1.5rem;
		width: 100vw;
		@media screen and (min-width: $screen-t) {
			margin: 0 -4rem;
		}
		@media screen and (min-width: $screen-d) {
			display: none;
		}
		&::after {
			background: var(--front-brand_50p);
			bottom: .1rem;
			content: '';
			display: block;
			height: .1rem;
			left: 0;
			position: absolute;
			width: 100%;
		}
	}
	&__nav-slide,
	&__slider-pagination-bullet {
		color: var(--front-primary_75p);
		cursor: pointer;
		@include map-slider-nav;
		padding: 2rem 0;
		position: relative;
		text-transform: uppercase;
		width: auto;
		&::after {
			background: var(--front-brand);
			bottom: 0;
			content: '';
			display: block;
			height: 0;
			left: 0;
			position: absolute;
			transition: height .25s ease;
			width: 100%;
		}
		&_active {
			color: var(--front-primary);
			&::after {
				height: .3rem;
			}
		}
	}
	&__slider-button {
		align-items: center;
		bottom: 1rem;
		cursor: pointer;
		display: none;
		justify-content: center;
		height: 4rem;
		position: absolute;
		width: 4rem;
		z-index: 2;
		@media screen and (min-width: $screen-d) {
			display: flex;
		}
		&_disabled {
			opacity: .5;
			pointer-events: none;
		}
	}
	&__slider-button_next {
		right: 0;
	}
	&__slider-button_prev {
		right: 4rem;
	}
	&__icon {
		stroke: var(--front-brand);
		height: 2.5rem;
		width: 2.5rem;
		.map__slider-button_disabled & {
			stroke: var(--front-brand_50p);
		}
	}
}
