// В этом файле должны быть стили для БЭМ-блока events, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.events {
	&__inner {
		display: grid;
		gap: 4rem;
	}
	&__pagination {
		margin-top: 3rem;
		@media screen and (min-width: $screen-ti) {
			margin-top: 5rem;
		}
	}
	#eventsDatePicker {
		display: none;
	}
}
