:root {
	--color_black: #000000;
	--color_white: #ffffff;

	--font_base: var(--font_primary);
	--font_primary: 'Berlin Type', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Helvetica Neue', Arial, sans-serif;

	--body_bg: var(--color_white);
	--body_color: var(--color_black);

	--font_size_root: 62.5%;
	--font_size_base: 1.6rem;
	--font_weight_base: 400;

	--line_height_base: 100%;

	--front-brand:          #0069b4;
	--front-brand_50p:      rgba(0, 105, 180, 0.5);
	--front-primary:        #23282d;
	--front-primary_75p:    rgba(35, 40, 45, 0.75);
	--front-primary_50p:    rgba(35, 40, 45, 0.5);
	--front-primary_25p:    rgba(35, 40, 45, 0.25);
	--front-secondary:      #c8cdd2;
	--front-invert:         #ffffff;
	--front-invert_75p:     rgba(255, 255, 255, 0.75);
	--front-invert_50p:     rgba(255, 255, 255, 0.5);
	--front-invert_25p:     rgba(255, 255, 255, 0.25);
	--front-warning:        #c10d0d;
	--front-alarm:          #dc7106;
	--front-yellow:         #efac00;
	--front-violet:         #cf1a92;
	--front-cyan:           #05c3dd;
	--front-green:          #21d803;

	--back-brand:           #0069b4;
	--back-brand-dark:      #005999;
	--back-brand_75p:       rgba(0, 105, 180, 0.75);
	--back-primary:         #ffffff;
	--back-primary_75p:     rgba(255, 255, 255, 0.75);
	--back-primary_50p:     rgba(255, 255, 255, 0.5);
	--back-primary_25p:     rgba(255, 255, 255, 0.25);
	--back-secondary:       #dbedff;
	--back-tertiary:        #f0f7ff;
	--back-darker:          rgba(0, 89, 153, 0.5);
	--back-danger:          #f6dbdb;
	--back-success:         #bde9c4;

	--back-header-gradient: linear-gradient(180deg,
			rgba(0, 105, 180, 0.85) 0,
			rgba(0, 105, 180, 0.85) 50%,
			rgba(0, 105, 180, 0.84265) 53.33%,
			rgba(0, 105, 180, 0.819817) 56.67%,
			rgba(0, 105, 180, 0.780589) 60%,
			rgba(0, 105, 180, 0.7247) 63.33%,
			rgba(0, 105, 180, 0.652991) 66.67%,
			rgba(0, 105, 180, 0.567899) 70%,
			rgba(0, 105, 180, 0.473712) 73.33%,
			rgba(0, 105, 180, 0.376288) 76.67%,
			rgba(0, 105, 180, 0.282101) 80%,
			rgba(0, 105, 180, 0.197009) 83.33%,
			rgba(0, 105, 180, 0.1253) 86.67%,
			rgba(0, 105, 180, 0.0694109) 90%,
			rgba(0, 105, 180, 0.0301835) 93.33%,
			rgba(0, 105, 180, 0.00735012) 96.67%,
			rgba(0, 105, 180, 0) 100%);

	--border-brand:         #0069b4;
	--border-brand-light:   #dbedff;
	--border-brand_50p:     rgba(0, 105, 180, 0.5);
	--border-dark:          #c8c9cb;
	--border-dark_50p:      rgba(200, 201, 203, 0.5);
	--border-light:         #ffffff;
	--border-light_50p:     rgba(255, 255, 255, 0.5);
}

$screen-m:      0 !default;
$screen-t:      576px !default;
$screen-ti:     768px !default;
$screen-tl:     992px !default;
$screen-d:      1280px !default;
$screen-dl:     1440px !default;
$screen-dxl:    1680px !default;
