// В этом файле должны быть стили для БЭМ-блока textarea, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.textarea {
	appearance: none;
	border: .1rem solid var(--border-brand_50p);
	@include textarea;
	height: 12rem;
	outline: none;
	padding: 1.4rem 1.9rem;
	resize: none;
	transition: border .25s ease-out;
	&::placeholder {
		color: var(--front-primary_50p);
	}
	&_has-value,
	&:focus {
		border: .1rem solid var(--border-brand);
	}
	&[disabled] {
		background: var(--back-tertiary);
		border: .1rem solid var(--border-brand-light);
		cursor: not-allowed;
		&::placeholder {
			color: var(--front-primary_25p);
		}
	}
}
