// В этом файле должны быть стили для БЭМ-блока people-tile, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.person {
	border: .1rem solid var(--border-dark_50p);
	color: var(--front-brand);
	display: flex;
	gap: 1.5rem;
	padding: .9rem;
	text-decoration: none;
	transition: border .25s ease;
	@media screen and (min-width: $screen-t) {
		gap: 2rem;
		padding: 1.9rem;
	}
	&_contacts {
		padding-left: 8.9rem;
		@media screen and (min-width: $screen-t) {
			padding-left: 11.9rem;
		}
	}
	&[href] {
		border: .1rem solid var(--border-brand-light);
		&:hover {
			border-color: var(--border-brand);
		}
	}
	&__img {
		border-radius: 50%;
		display: block;
		flex: 0 0 auto;
		height: 6.5rem;
		width: 6.5rem;
		@media screen and (min-width: $screen-t) {
			height: 8rem;
			width: 8rem;
		}
	}
	&__icon {
		display: block;
		flex: 0 0 auto;
		height: 6.4rem;
		stroke: var(--front-brand);
		width: 6.4rem;
		@media screen and (min-width: $screen-t) {
			margin: .8rem;
		}
	}
	&__inner {
		align-items: flex-start;
		display: flex;
		flex-direction: column;
	}
	&__title {
		color: inherit;
		@include person-title(400);
		margin-bottom: .5rem;
		text-decoration: none;
		transition: opacity .25s ease;
		&[href]:hover {
			opacity: .8;
		}
		span {
			font-weight: 700;
		}
	}
	&[href^='http'] &__title::after {
		background: url('../img/svgSprite.svg#icon__external') no-repeat;
		background-size: 100% 100%;
		content: '';
		display: inline-block;
		height: 2.5rem;
		margin-left: .5rem;
		width: 2.5rem;
	}
	&__descr {
		color: var(--front-primary_75p);
		@include person-descr;
	}
	&__info {
		display: grid;
		gap: 1.5rem;
	}
	&__title + &__info,
	&__descr + &__info {
		margin-top: 1.5rem;
	}
	&__info-row {
		display: grid;
		gap: .5rem;
	}
	&__link {
		color: inherit;
		@include person-descr(700);
		text-decoration: none;
		transition: opacity .25s ease;
		&[href]:hover {
			opacity: .8;
		}
	}
}
