// В этом файле должны быть стили для БЭМ-блока tags, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.tags {
	&__label {
		color: var(--front-primary_50p);
		@include style_1\2_1\25;
		margin-bottom: 1.5rem;
		text-transform: uppercase;
	}
	&__inner {
		display: flex;
		flex-wrap: wrap;
		gap: 1rem;
	}
	&__item {
		background: var(--back-tertiary);
		border: .1rem solid var(--border-brand-light);
		color: inherit;
		@include style_1\5_1;
		padding: .9rem 1.4rem;
		text-decoration: none;
		transition: border .25s ease;
		white-space: nowrap;
		&:hover {
			border: .1rem solid var(--border-brand);
		}
	}
}
