// В этом файле должны быть стили для БЭМ-блока lk-nav, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.lk-nav {
	height: 2rem;
	margin: 0 -1.5rem;
	position: sticky;
	overflow: hidden;
	@media screen and (min-width: $screen-t) {
		margin: 0 -4rem;
	}
	@media screen and (min-width: $screen-tl) {
		height: auto;
		margin: 0;
	}
	&__scroll {
		align-items: flex-start;
		display: flex;
		height: 5rem;
		overflow: hidden;
-webkit-overflow-scrolling: touch;
		overflow-x: scroll;
		overflow-y: hidden;
		&::after,
		&::before {
			content: '';
			flex: 0 0 auto;
			height: 100%;
			width: 1.5rem;
		}
		@media screen and (min-width: $screen-t) {
			&::after,
			&::before {
				width: 4rem;
			}
		}
		@media screen and (min-width: $screen-tl) {
			& {
				height: auto;
				overflow: hidden;
			}
			&::after,
			&::before {
				content: none;
			}
		}
	}
	&__inner {
		display: flex;
		gap: 2.5rem;
		list-style: none;
		margin: 0;
		padding: 0;
		@media screen and (min-width: $screen-ti) {
			gap: 4rem;
		}
		@media screen and (min-width: $screen-tl) {
			display: grid;
			gap: 2.5rem;
			padding: 0;
		}
	}
	&__item {
		@media screen and (min-width: $screen-tl) {
			&:last-of-type {
				margin-top: 2.5rem;
			}
		}
	}
	&__link {
		color: inherit;
		cursor: pointer;
		@include style_1\8_1\12(400);
		text-decoration: none;
		transition: opacity .25s ease;
		&:hover {
			opacity: .75;
		}
		&_active {
			color: var(--front-brand);
			font-weight: 700;
			pointer-events: none;
		}
	}
}
