// В этом файле должны быть стили для БЭМ-блока advantages, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.advantages {
	display: grid;
	gap: 5rem;
	@media screen and (min-width: $screen-ti) {
		gap: 8rem;
	}
	&__row {}
	&__title {
		color: var(--front-brand);
		@include advantages-title;
	}
	&__list {
		align-items: flex-start;
		display: flex;
		flex-direction: column;
		gap: 2rem;
		list-style: none;
		margin: 1.5rem 0 0;
		padding: 0;
		@media screen and (min-width: $screen-ti) {
			display: grid;
			gap: 2rem;
			grid-template-columns: repeat(3, 1fr);
			margin: 2.5rem 0 0;
		}
	}
	&__item {
		border-top: .1rem solid var(--front-brand);
		color: var(--front-primary_75p);
		@include advantages-item;
		padding-top: .5rem;
	}
	:is(.advantages) &__button {
		background: var(--back-brand);
		border: none;
		color: var(--front-invert);
		@include advantages-button;
		max-width: 42rem;
		padding: 1rem 2rem;
		@media screen and (min-width: $screen-t) {
			margin: 0 auto;
		}
		@media screen and (min-width: $screen-ti) {
			margin: 0;
			padding: 1.5rem 3rem;
		}
		&:hover {
			border: none;
		}
	}
}
