// В этом файле должны быть стили для БЭМ-блока contacts-list, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.contacts-list {
	display: grid;
	gap: 1.5rem;
	&__item {
		display: grid;
	}
	&__person {
		color: var(--front-brand);
		@include contacts-list-person;
		margin-bottom: .5rem;
		text-decoration: none;
		&:hover {
			text-decoration: underline;
		}
		span {
			font-weight: 700;
		}
	}
	&__link {
		color: var(--front-primary_75p);
		@include style_1\5_1\33;
		text-decoration: none;
		&:hover {
			text-decoration: underline;
		}
	}
}
