// В этом файле должны быть стили для БЭМ-блока box, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.box {
	border: 1px solid var(--border-brand-light);
	display: flex;
	flex-direction: column;
	gap: 2rem;
	padding: 1.4rem;
	@media screen and (min-width: $screen-t) {
		padding: 2.9rem;
	}
	&__title {
		color: var(--front-primary_50p);
		@include style_1\2_1\25;
		text-transform: uppercase;
	}
	&__list {
		display: grid;
		gap: 1.5rem;
		:is(.article__side) & {
			gap: 2.5rem;
		}
	}
	&__footer {
		display: flex;
		flex-wrap: wrap;
		gap: 1.5rem;
		.button_solid {
			flex: 1 1 auto;
		}
	}
	&__footer-inner {
		display: flex;
		gap: 1.5rem;
	}
}
