// В этом файле должны быть стили для БЭМ-блока footer, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.footer {
	background: var(--back-brand);
	color: var(--front-invert);
	padding: 4rem 0 3.75rem;
	@media screen and (min-width: $screen-d) {
		padding: 6rem 0 5rem;
	}
	&__inner {
		display: grid;
		grid-template-areas:
			'logo'
			'links'
			'actions'
			'row';
		@media screen and (min-width: $screen-t) {
			grid-template-columns: 4fr 8fr;
			grid-template-areas:
				'logo links'
				'. actions'
				'. row';
			margin: 0 -1rem;
		}
		@media screen and (min-width: $screen-ti) {
			grid-template-columns: 3fr 9fr;
		}
		@media screen and (min-width: $screen-d) {
			gap: 10rem 0;
			grid-template-columns: 3fr 5fr 4fr;
			grid-template-areas:
				'logo links actions'
				'. row row';
		}
		@media screen and (min-width: $screen-dxl) {
			grid-template-columns: 3fr 6fr 3fr;
		}
	}
	&__col {
		@media screen and (min-width: $screen-t) {
			padding: 0 1rem;
		}
		&:nth-child(1) {
			grid-area: logo;
		}
		&:nth-child(2) {
			grid-area: links;
			margin: 4.5rem 0 5.5rem;
			@media screen and (min-width: $screen-t) {
				margin: 0 0 5.5rem;
				padding-top: .5rem;
			}
			@media screen and (min-width: $screen-d) {
				margin: 0;
			}
		}
		&:nth-child(3) {
			align-items: flex-start;
			display: flex;
			grid-area: actions;
			flex-direction: column;
			@media screen and (min-width: $screen-d) {
				align-items: flex-end;
			}
		}
		&:nth-child(4) {
			grid-area: row;
			margin-top: 8rem;
			@media screen and (min-width: $screen-t) {
				margin-top: 6rem;
			}
			@media screen and (min-width: $screen-d) {
				margin: 0;
			}
		}
	}
	&__logo {
		display: block;
		height: 12rem;
		width: 11.5rem;
		&[href] {
			transition: opacity .25s ease;
			&:hover {
				opacity: .75;
			}
		}
	}
	&__logo-img {
		fill: var(--front-invert);
		height: 100%;
		width: 100%;
	}
	&__links {}
	&__banners {
		margin-bottom: auto;
		max-width: 38.5rem;
		width: 100%;
	}
	&__actions {
		align-items: flex-start;
		color: var(--front-invert);
		display: flex;
		flex-direction: column;
		margin-top: 4rem;
		width: 100%;
		@media screen and (min-width: $screen-d) {
			align-items: flex-end;
		}
	}
	&__socials {
		align-items: flex-start;
		display: flex;
		flex-direction: column;
		@media screen and (min-width: $screen-d) {
			align-items: flex-end;
		}
	}
	&__social {
		color: inherit;
		@include footer-social;
		text-decoration: none;
		transition: opacity .25s ease;
		&:hover {
			opacity: .75;
		}
		& + & {
			margin-top: .5rem;
		}
		&_tg {
			margin-bottom: .5rem;
			text-transform: uppercase;
		}
	}
	&__policy {
		margin-top: 3rem;
		@media screen and (min-width: $screen-ti) {
			text-align: right;
		}
	}
	&__policy-link {
		color: inherit;
		@include style_1\5_1\33(700);
		text-decoration: none;
		transition: opacity .25s ease;
		white-space: nowrap;
		&:hover {
			opacity: .75;
		}
	}
	&__button-wrap {
		display: flex;
		flex-direction: column;
		gap: 1rem;
		margin-top: 2rem;
		width: 25rem;
		@media screen and (min-width: $screen-ti) {
			flex-direction: row;
			width: 38.5rem;
			& > * {
				flex: 1 0 auto;
			}
		}
	}
	&__row {
		display: flex;
		flex-direction: column;
		gap: 1.5rem;
		@media screen and (min-width: $screen-tl) {
			align-items: center;
			flex-direction: row;
			gap: 0;
			justify-content: space-between;
			padding-right: 8.5rem;
		}
	}
	&__copy {
		@include footer-copy;
		position: relative;
		text-transform: uppercase;
		&::before {
			content: attr(data-year) ' ©';
			position: absolute;
			transform: translateY(-100%);
		}
		@media screen and (min-width: $screen-t) {
			&::before {
				transform: translateX(calc(-100% - 2rem));
			}
		}
	}
	&__madein {
		@include style_1\5_1\33;
		display: flex;
		justify-content: flex-start;
		gap: 1rem;
	}
	&__madein-link {
		margin: -.25rem 0;
		transition: opacity .25s ease;
		&:hover {
			opacity: .75;
		}
	}
	&__madein-logo {
		aspect-ratio: 113/25;
		display: block;
		height: 2.5rem;
		width: auto;
	}
}
