// В этом файле должны быть стили для БЭМ-блока lk, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.lk {
	&__subtitle {
		color: var(--front-brand);
		@include style_1\2_1\25;
		text-transform: uppercase;
		text-decoration: none;
		transition: opacity;
	}
	&__header {
		align-items: flex-start;
		display: flex;
		flex-direction: column;
		gap: 1.5rem;
		margin-bottom: 3.5rem;
		@media screen and (min-width: $screen-ti) {
			margin-bottom: 7rem;
		}
	}
	&__title {
		@include school-title;
		margin: 1.5rem 0 0;
		position: relative;
		@media screen and (min-width: $screen-ti) {
			max-width: 92.5rem;
		}
		&::before {
			color: var(--front-primary_50p);
			content: attr(data-subtitle);
			@include section-subtitle;
			position: absolute;
			top: -1.5rem;
			text-transform: uppercase;
		}
	}
	&__my-page {
		align-items: center;
		border: .1rem solid var(--border-brand-light);
		color: var(--front-brand);
		display: inline-flex;
		@include style_1\5_1(400);
		gap: .5rem;
		padding: .5rem .5rem .5rem 1.5rem;
		text-decoration: none;
		transition: border .25s ease, opacity .25s ease;
		&:hover {
			border: .1rem solid var(--border-brand);
			opacity: .8;
		}
	}
	&__my-page-icon {
		height: 2.5rem;
		stroke: var(--front-primary);
		width: 2.5rem;
	}
	&__form {
		display: grid;
		gap: 5rem;
		max-width: 71.5rem;
		@media screen and (min-width: $screen-tl) {
			gap: 8rem;
		}
		.form__submit {
			width: 100%;
			@media screen and (min-width: $screen-t) {
				width: 25rem;
			}
		}
	}
	&__form-title {
		color: var(--front-primary_50p);
		@include grid-subtitle;
	}
}
