// В этом файле должны быть стили для БЭМ-блока form-message, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.form-message {
	display: none;
	@include style_1\5_1\33;
	padding: 1rem 2rem;
	width: 100%;
	&_error {
		background: var(--back-danger);
	}
	&_success {
		background: var(--back-success);
	}
}
