// В этом файле должны быть стили для БЭМ-блока drop-search, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.drop-search {
	background: var(--back-primary);
	border: .1rem solid var(--border-dark);
	list-style: none;
	margin: 0;
	max-height: 15rem;
-webkit-overflow-scrolling: touch;
	overflow: hidden;
	overflow-y: auto;
	padding: 0;
	position: absolute;
	text-align: left;
	top: calc(100% + .5rem);
	width: 100%;
	z-index: 96;
	&__loader {
		align-items: center;
		display: flex;
		height: 3.5rem;
		justify-content: center;
		overflow: hidden;
		padding: 0 1rem;
	}
	&__message {
		color: var(--front-primary_50p);
		@include style_1\5_1;
		padding: 1rem 2rem;
		text-align: center;
	}
	&__link {
		color: inherit;
		display: block;
		@include style_1\5_1;
		padding: 1rem 2rem;
		text-decoration: none;
		transition: background-color .2s ease;
		&:hover {
			background-color: var(--back-tertiary);
		}
	}
}
