@mixin button-title {
	font-feature-settings: 'pnum' on, 'lnum' on;
	font-size: 1.2rem;
	font-weight: 700;
	letter-spacing: 0.04em;
	line-height: inherit;
}
@mixin button-title-large {
	font-feature-settings: 'pnum' on, 'lnum' on;
	font-size: 1.5rem;
	font-weight: 700;
	letter-spacing: 0.02em;
	line-height: inherit;
}

@mixin popup-title {
	font-size: 2.5rem;
	font-weight: 400;
	line-height: 1;
	@media screen and (min-width: $screen-t) {
		line-height: 1.4;
	}
}

@mixin form-small {
	font-size: 1.1rem;
	line-height: 1.3637;
}

@mixin input {
