	letter-spacing: 0.02em;
	line-height: 1;
}
@mixin textarea {
	font-size: 1.5rem;
	letter-spacing: 0.02em;
	line-height: 1.3334;
}

@mixin slider-title {
-webkit-box-orient: vertical;
	display: -webkit-box;
	font-size: 2rem;
	font-weight: 700;
	letter-spacing: 0.03em;
-webkit-line-clamp: 5;
	line-height: 1.2;
	@media screen and (min-width: $screen-t) {
		font-size: 2.5rem;
	}
	@media screen and (min-width: $screen-tl) {
-webkit-line-clamp: 4;
	}
	@media screen and (min-width: $screen-d) {
		font-size: 3rem;
		letter-spacing: 0.01em;
		line-height: 1.1667;
	}
}
@mixin slider-descr {
-webkit-box-orient: vertical;
	display: -webkit-box;
	font-size: 1.5rem;
-webkit-line-clamp: 6;
	line-height: 1.3334;
	@media screen and (min-width: $screen-tl) {

	}
	@media screen and (min-width: $screen-d) {
-webkit-line-clamp: 4;
		line-height: 1.6667;
	}
}
