.hystmodal__opened {
	left: 0;
	overflow: hidden;
	position: fixed;
	right: 0;
}
.hystmodal__shadow {
	backdrop-filter: blur(.4rem);
	background: var(--back-darker);
	border: none;
	bottom: 0;
	display: block;
	left: 0;
	opacity: 0;
	overflow: hidden;
	pointer-events: none;
	position: fixed;
	right: 0;
	top: 0;
	transition: opacity .25s ease;
	width: 100%;
	z-index: 98;
}
.hystmodal__shadow--show {
	opacity: 1;
	pointer-events: auto;
	transition: opacity .25s;
}
.hystmodal {
	bottom: 0;
	display: flex;
	flex-flow: column nowrap;
	justify-content: flex-start;
	left: 0;
	opacity: 1;
	overflow: hidden;
-webkit-overflow-scrolling: touch;
	overflow-y: auto;
	pointer-events: none;
	position: fixed;
	right: 0;
	top: 0;
	visibility: hidden;
	z-index: 99;
}
.hystmodal--active {
	opacity: 1;
}
.hystmodal--moved,
.hystmodal--active {
	pointer-events: auto;
	visibility: visible;
}
.hystmodal__wrap {
	align-items: center;
	display: flex;
	flex-flow: column nowrap;
	flex-grow: 0;
	flex-shrink: 0;
	justify-content: center;
	margin: auto;
	min-height: 100%;
	width: 100%;
}
.hystmodal__window {
	background: var(--back-primary);
	box-sizing: border-box;
	flex-grow: 0;
	flex-shrink: 0;
	margin: 5rem 0;
	max-width: 100%;
	opacity: 0;
	overflow: visible;
	transform: scale(0.9);
	transition: transform 0.2s ease 0s, opacity 0.2s ease 0s;
}
.hystmodal--active .hystmodal__window {
	opacity: 1;
	transform: scale(1);
}
.hystmodal__close {
	background: transparent;
	border: none;
	cursor: pointer;
	display: block;
	font-size: 0;
	outline: none;
	padding: 0;
	position: absolute;
	right: 0;
	top: 0;
	z-index: 10;
}
@media screen and (max-width: $screen-ti) {
	.hystmodal__window {
		margin: 0;
	}
}
