// В этом файле должны быть стили для БЭМ-блока event-tile, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.event-tile {
	align-items: start;
	border: .1rem solid var(--border-brand-light);
	display: grid;
	gap: 2rem;
	grid-template-columns: 5rem auto;
	padding: 1.4rem;
	@media screen and (min-width: $screen-t) {
		gap: 3.5rem;
		grid-template-columns: 5rem auto;
		padding: 1.4rem 2.4rem 1.9rem 1.4rem;
	}
	&__date {
		display: flex;
		flex-direction: column;
		justify-content: center;
	}
	&__month {
		color: var(--front-primary_50p);
		@include style_1\5_1(700);
	}
	&__day {
		color: var(--back-brand);
		@include event-day;
	}
	&__info {
		display: grid;
		gap: 1.5rem;
		@media screen and (min-width: $screen-t) {
			padding-top: .5rem;
		}
	}
	&__row {
		display: flex;
		justify-content: space-between;
		gap: 2rem;
	}
	&__wrapper {
		display: flex;
		flex-wrap: wrap;
		gap: .5rem;
		transform: translateX(-1rem);
	}
	&__tag {
		border: .1rem solid transparent;
		color: inherit;
		@include style_1\2_1\25;
		padding: .4rem .9rem;
		text-decoration: none;
		text-transform: uppercase;
		transition: opacity .25s ease;
		&:hover {
			opacity: .75;
		}
		&_international {
			border-color: var(--front-warning);
		}
		&_local {
			border-color: var(--front-yellow);
		}
		&_interregional {
			border-color: var(--front-cyan);
		}
		&_regional {
			border-color: var(--front-violet);
		}
		&_intraschool {
			border-color: var(--front-green);
		}
		&_internationalDay {
			background: var(--back-secondary);
			border-color: var(--border-brand-light);
		}
	}
	&__region {
		color: var(--front-primary_75p);
		@include style_1\5_1;
		text-decoration: none;
	}
	&__link {
		color: var(--back-brand);
		@include event-title;
		text-decoration: none;
		text-transform: uppercase;
		&:hover {
			text-decoration: underline;
		}
	}
}
