// В этом файле должны быть стили для БЭМ-блока wysiwyg, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.wysiwyg {
	h2 {
		@include article-h2;
		margin: 5rem 0 2rem;
		@media screen and (min-width: $screen-ti) {
			margin: 7.5rem 0 4rem;
		}
	}
	h3 {
		@include article-h3;
		margin: 3.5rem 0 2rem;
		@media screen and (min-width: $screen-ti) {
			margin: 6rem 0 2.5rem;
		}
	}
	p {
		@include paragraph;
		margin: 0 0 2rem;
		@media screen and (min-width: $screen-ti) {
			margin: 0 0 4rem;
		}
	}
	a {
		color: var(--front-brand);
		font-weight: 700;
		text-decoration: none;
		&:hover {
			text-decoration: underline;
		}
	}
	figure {
		border-left: 1rem solid var(--back-secondary);
		margin: 0 0 2rem;
		padding-left: 1.5rem;
		@media screen and (min-width: $screen-ti) {
			margin: 0 0 4rem;
			padding-left: 2.5rem;
		}
		p {
			color: var(--front-primary_75p);
			margin-bottom: 1.5rem;
			@media screen and (min-width: $screen-ti) {
				margin-bottom: 1.5rem;
			}
		}
	}
	figcaption {
		div {
			color: var(--front-primary_75p);
			@include cite-sub;
		}
	}
	blockquote {
		margin: 0;
	}
	cite {
		@include style_1\5_1\33(700);
		font-style: normal;
	}
	ol,
	ul {
		counter-reset: item;
		list-style: none;
		margin: 0 0 2rem;
		padding: 0;
		@media screen and (min-width: $screen-ti) {
			margin: 0 0 4rem;
		}
		li {
			@include paragraph;
			position: relative;
		}
		li + li {
			margin-top: 1rem;
		}
	}
	ol li {
		padding-left: 3rem;
		&::before {
			content: counter(item)'.';
			counter-increment: item;
			display: block;
			font-weight: 700;
			left: 0;
			position: absolute;
			top: 0;
		}
	}
	ul li {
		padding-left: 2rem;
		&::before {
			background: var(--front-brand);
			border-radius: .3rem;
			top: 0;
			content: '';
			display: block;
			height: .5rem;
			left: 0;
			position: absolute;
			transform: translate(.6rem, 1.1rem);
			width: .5rem;
		}
	}
	p + ol,
	p + ul {
		margin-top: -1.5rem;
		@media screen and (min-width: $screen-ti) {
			margin-top: -2.5rem;
		}
	}
	& > :last-child {
		margin-bottom: 0 !important;
	}
}
