// В этом файле должны быть стили для БЭМ-блока checkbox, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.checkbox {
	cursor: pointer;
	display: flex;
	justify-self: start;
	gap: 1.5rem;
	&__input {
		display: none;
	}
	&__fake-input {
		border: .1rem solid var(--front-brand_50p);
		height: 2rem;
		transition: background .125s ease;
		width: 2rem;
		&::after {
			border: .3rem solid var(--border-light);
			border-top: none;
			border-right: none;
			content: '';
			display: block;
			height: .75rem;
			margin: .4rem .3rem;
			transform: rotate(-45deg);
			width: 1.25rem;
		}
	}
	&__input:checked + &__fake-input {
		background: var(--back-brand);
	}
	&__label {
		@include style_1\5_1\33;
		user-select: none;
	}
	&__input:checked ~ &__label {
		font-weight: 700;
	}
}
