// В этом файле должны быть стили для БЭМ-блока hero-slider, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.hero-slider {
	color: var(--front-invert);
	display: grid;
	gap: 1rem;
	padding-top: 1rem;
	@media screen and (min-width: $screen-tl) {
		align-content: center;
		gap: 0;
		grid-template-columns: 3fr 6fr;
		height: 100%;
	}
	&__col {
		overflow: hidden;
		@media screen and (min-width: $screen-tl) {
			padding: 0 1rem;
		}
		@media screen and (min-width: $screen-d) {
			&:nth-child(1) {
				padding-left: calc(33.3334% + 1rem);
			}
			&:nth-child(2) {
				padding-right: calc(16.6667% + 1rem);
			}
		}
	}
	&__controls {
		display: grid;
		gap: 3.3rem;
		width: 100%;
		.buttons-set {
			justify-self: start;
		}
		&_desktop {
			display: none;
		}
		@media screen and (min-width: $screen-tl) {
			&_mobile {
				display: none;
			}
			&_desktop {
				align-items: flex-end;
				bottom: 0;
				display: flex;
				flex: 1 1 auto;
				flex-direction: column;
				justify-content: space-between;
			}
			.buttons-set {
				justify-self: end;
			}
		}
	}
	&__pagination {
		display: flex;
		gap: .5rem;
		justify-self: start;
		@media screen and (min-width: $screen-d) {
			justify-content: flex-end;
		}
		.swiper-pagination-bullet {
			background: var(--back-primary);
			border-radius: .2rem;
			opacity: .5;
			height: .2rem;
			transition: opacity .25s ease;
			width: 2rem;
		}
		.swiper-pagination-bullet-active {
			opacity: 1;
		}
	}
	&__prev,
	&__custom-prev,
	&__next,
	&__custom-next {
		padding: .4rem;
	}
	&__prev {}
	&__next {}
	&__icon {
		height: 2.5rem;
		width: 2.5rem;
	}

	&__main-slider,
	&__img-slider {
		@media screen and (min-width: $screen-tl) {
			display: flex;
			flex-direction: column;
			gap: 1.5rem;
			justify-content: space-between;
			height: 26rem;
		}
	}
	&__main-slider {
		display: grid;
		gap: 2rem;
	}
	&__img-slider {
		margin: 0 -1rem;
		@media screen and (min-width: $screen-tl) {
			margin: 0;
		}
	}
	&__img {
		aspect-ratio: 25/14;
		border: .1rem solid var(--front-invert);
		display: block;
		height: auto;
		margin-left: 1rem;
		width: calc(60% - 2rem);
		@media screen and (min-width: $screen-tl) {
			margin: 0;
			width: 100%;
		}
	}
	&__link {
		color: inherit;
		text-decoration: none;
		transition: opacity .25s ease;
		&:hover {
			opacity: .8;
		}
	}
	&__title {
		@include slider-title;
		margin: 0;
		overflow: hidden;
		text-transform: uppercase;
	}
	&__descr {
		@include slider-descr;
		margin-top: 2rem;
		overflow: hidden;
		@media screen and (min-width: $screen-tl) {
			width: 80%;
		}
		@media screen and (min-width: $screen-d) {
			width: 80%;
		}
	}
	.swiper-notification {
		display: none;
	}
}
