// В этом файле должны быть стили для БЭМ-блока footer-banners, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.footer-banners {
	display: grid;
	gap: 1rem;
	grid-template-columns: repeat(3, 1fr);
	&__item {
		outline: .1rem solid var(--border-light_50p);
		outline-offset: -.1rem;
		transition: outline .25s ease;
		&:hover {
			outline: .1rem solid var(--border-light);
		}
	}
	&__icon {
		aspect-ratio: 12/10;
		display: block;
		fill: var(--front-invert);
		height: auto;
		width: 100%;
	}
}
