// В этом файле должны быть стили для БЭМ-блока school, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.school {
	&__header {
		display: flex;
		flex-direction: column;
		position: relative;
		@media screen and (min-width: $screen-tl) {
			flex-direction: row;
			gap: 4rem;
		}
	}
	&__logo {
		display: none;
		height: 10rem;
		left: 0;
		position: absolute;
		transform: translateX(calc(-100% - 2rem));
		width: 10rem;
		@media screen and (min-width: $screen-tl) {
			display: block;
		}
	}
	&__inner {}
	&__subtitle {
		color: var(--front-brand);
		@include style_1\2_1\25;
		text-transform: uppercase;
		text-decoration: none;
		transition: opacity;

	}
	&__title {
		@include school-title;
		margin: 0 0 1rem;
		@media screen and (min-width: $screen-ti) {
			margin: 0 0 1.5rem;
			max-width: 79rem;
		}
	}
	&__descr {
		@include style_1\5_1\33;
		max-width: 52rem;
	}
	&__icon {
		height: 9.6rem;
		margin: 2rem 0 0;
		width: 9.2rem;
		@media screen and (min-width: $screen-tl) {
			justify-self: flex-end;
			height: 12rem;
			margin: 3rem 0 0 auto;
			width: 11.5rem;
		}
	}

	:is(&) .section__grid + .section__grid {
		margin-top: 3rem;
		@media screen and (min-width: $screen-ti) {
			margin-top: 8rem;
		}
	}
}
