// В этом файле должны быть стили для БЭМ-блока footer-links, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.footer-links {
	@media screen and (min-width: $screen-ti) {
		column-count: 2;
		gap: 2rem;
	}
	@media screen and (min-width: $screen-tl) {
		column-count: 3;
	}
	@media screen and (min-width: $screen-d) {
		column-count: 2;
	}
	@media screen and (min-width: $screen-dxl) {
		column-count: 3;
	}
	&__section {
		align-items: flex-start;
		break-inside: avoid-column;
		display: flex;
		flex-direction: column;
		& + & {
			margin-top: 3.5rem;
		}
	}
	&__title {
		color: var(--front-invert);
		@include style_1\8_1\12(700);
		text-decoration: none;
		transition: opacity .25s ease;
		&:hover {
			opacity: .75;
		}
	}
	&__list {
		list-style: none;
		margin: 1.5rem 0 0;
		padding: 0;
	}
	&__item {
		display: flex;
		& + & {
			margin-top: 1rem;
		}
	}
	&__link {
		color: var(--front-invert);
		@include style_1\5_1\33;
		text-decoration: none;
		transition: opacity .25s ease;
		&:hover {
			opacity: .75;
		}
	}
}
