// В этом файле должны быть стили для БЭМ-блока registration-tiles, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.registration-tiles {
	display: grid;
	gap: 1rem;
	@media screen and (min-width: $screen-t) {
		gap: 2rem;
		grid-template-columns: repeat(2, 1fr);
	}
	@media screen and (min-width: $screen-d) {
		grid-template-columns: repeat(3, 1fr);
	}
	@media screen and (min-width: $screen-dl) {
		grid-template-columns: repeat(4, 1fr);
	}
	&__item {
		align-items: flex-start;
		border: .1rem solid var(--border-brand-light);
		color: inherit;
		flex-direction: column;
		padding: 1.5rem;
		text-decoration: none;
		transition: border .25s ease, transform .25s ease;
		&:hover {
			border: .1rem solid var(--border-brand);
			transform: translateY(-.5rem);
		}
	}
	&__icon {
		height: 3.5rem;
		stroke: var(--front-brand);
		width: 3.5rem;
	}
	&__title {
		color: var(--front-brand);
		font-size: 1.2rem;
		font-weight: 700;
		letter-spacing: 0.04em;
		line-height: 1.25;
		margin-top: 1.5rem;
		text-transform: uppercase;
	}
	&__descr {
		@include style_1\5_1\33;
		letter-spacing: 0;
		margin-top: .5rem;
	}
}
