// В этом файле должны быть стили для БЭМ-блока lk-tabs, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.lk-tabs {
	display: grid;
	gap: 3rem;
	padding-top: 2rem;
	@media screen and (min-width: $screen-tl) {
		gap: 2rem;
		grid-template-columns: 2fr 5fr;
		padding: 0;
	}
	&__caption {
		height: 2rem;
		margin: 0 -1.5rem;
		position: sticky;
		overflow: hidden;
		@media screen and (min-width: $screen-t) {
			margin: 0 -4rem;
		}
		@media screen and (min-width: $screen-tl) {
			height: auto;
			margin: 0;
		}
	}
	&__scroll {
		align-items: flex-start;
		display: flex;
		height: 5rem;
		overflow: hidden;
-webkit-overflow-scrolling: touch;
		overflow-x: scroll;
		overflow-y: hidden;
		&::after,
		&::before {
			content: '';
			flex: 0 0 auto;
			height: 100%;
			width: 1.5rem;
		}
		@media screen and (min-width: $screen-t) {
			&::after,
			&::before {
				width: 4rem;
			}
		}
		@media screen and (min-width: $screen-tl) {
			height: auto;
			overflow: hidden;
			&::after,
			&::before {
				content: none;
			}
		}
	}
	&__head {
		display: flex;
		gap: 2rem;
		list-style: none;
		margin: 0;
		padding: 0;
		@media screen and (min-width: $screen-tl) {
			display: grid;
			justify-items: start;
			gap: 1.5rem;
		}
	}
	&__tab {
		color: inherit;
		cursor: pointer;
		@include style_1\5_1(400);
		transition: opacity .25s ease;
		white-space: nowrap;
		&:hover {
			opacity: .75;
		}
		@media screen and (min-width: $screen-t) {
			@include style_1\8_1\12(400);
		}
		&_active {
			color: var(--front-brand);
			pointer-events: none;
		}
	}
	&__body {}
	&__content {
		display: none;
		&_active {
			display: block;
		}
	}
}
