// В этом файле должны быть стили для БЭМ-блока entry-row, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.entry-row {
	display: flex;
	gap: .5rem;
	&__icon {
		flex: 1 1 auto;
		height: 1.5rem;
		stroke: var(--front-primary_75p);
		width: 1.5rem;
	}
	&__item {
		color: var(--front-primary_75p);
		display: block;
		@include style_1\2_1\25;
		text-transform: uppercase;
		white-space: nowrap;
		&[href] {
			color: var(--front-brand);
			text-decoration: none;
			transition: opacity .25s ease;
			&:hover {
				opacity: .8;
			}
		}
		& + &::before {
			color: var(--front-primary);
			content: '•';
			display: inline-block;
			margin-right: .5rem;
		}
	}
	&_invert &__icon {
		stroke: var(--front-invert_50p);
	}
	&_invert &__item {
		color: var(--front-invert_75p);
		&[href] {
			color: var(--front-invert_75p);
			text-decoration: none;
			transition: opacity .25s ease;
			&:hover {
				opacity: .6;
			}
		}
		& + .entry-row__item::before {
			color: var(--front-invert_75p);
			content: '•';
			display: inline-block;
			margin-right: .5rem;
		}
	}
}
