// В этом файле должны быть стили для БЭМ-блока about, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.about {
	@media screen and (min-width: $screen-tl) {
		background: url('../img/img__worldmap.svg') no-repeat center 9rem;
		background-size: 100% auto;
	}
	@media screen and (min-width: $screen-dxl) {
		background: url('../img/img__worldmap.svg') no-repeat center 9rem;
		background-size: 168rem auto;
	}
	&__nums {
		:is(.about) & {
			margin-top: 3rem;
			@media screen and (min-width: $screen-tl) {
				margin-top: 8rem;
			}
		}
		& + & {
			margin-top: 3rem;
			@media screen and (min-width: $screen-tl) {
				margin-top: 5rem;
			}
		}
		.section__subtitle {
			margin-bottom: 1rem;
			@media screen and (min-width: $screen-ti) {
				margin-top: 1.5rem;
			}
		}
	}
	&__row {
		display: flex;
		flex-wrap: wrap;
		gap: 1rem 0;
		@media screen and (min-width: $screen-ti) {
			gap: 0;
		}
	}
	&__item {
		align-items: flex-start;
		display: flex;
		flex: 1 1 auto;
		flex-direction: column;
		width: 50%;
		@media screen and (min-width: $screen-t) {
			width: 33.3333%;
		}
		@media screen and (min-width: $screen-d) {
			width: 33.3333%;
		}
		@media screen and (min-width: $screen-dl) {
			height: 14.5rem;
		}
	}
	&__num {
		color: var(--front-brand);
		@include about-num;
		transform: translateX(-.25rem);
	}
	&__descr {
		@include about-num-descr;
		transform: translate(.5rem, 0);
		@media screen and (min-width: $screen-dl) {
			transform: translate(1.5rem, -.5rem);
		}
	}
	&__tiles {
		display: grid;
		gap: 1rem;
		grid-template-columns: 1fr 1fr;
		margin-top: 2.5rem;
		@media screen and (min-width: $screen-ti) {
			grid-template-columns: repeat(3, 1fr);
		}
		@media screen and (min-width: $screen-d) {
			gap: 2rem;
			margin-top: 6rem;
		}
		@media screen and (min-width: $screen-dl) {
			grid-template-columns: repeat(4, 1fr);
		}
		@media screen and (min-width: $screen-dxl) {
			grid-template-columns: repeat(5, 1fr);
		}
	}
	&__tile {
		border: .1rem solid var(--border-brand-light);
		color: var(--front-primary_75p);
		display: flex;
		flex-direction: column;
		height: 16rem;
		padding: 1.4rem;
		text-decoration: none;
		transition: border .25s ease, color .25s ease, transform .25s ease;
		&:hover {
			border: .1rem solid var(--border-brand);
			color: var(--front-brand);
			transform: translateY(-.5rem);
		}
	}
	&__tile-title {
		color: inherit;
		@include about-tile;
	}
	&__tile-icon {
		height: 3.5rem;
		margin-top: auto;
		width: 3.5rem;
	}

	.lead,
	.paragraph {
		color: var(--front-primary_75p);
		max-width: 65.5rem;
	}
}
