// В этом файле должны быть стили для БЭМ-блока document-link, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.document-link {
	color: var(--front-primary);
	display: flex;
	gap: 1rem;
	text-decoration: none;
	&__icon {
		flex: 0 0 auto;
		height: 4rem;
		width: 4rem;
	}
	&__inner {
		display: grid;
		gap: .5rem;
		padding-top: .5rem;
	}
	&__title {
		color: inherit;
		@include style_1\5_1(700);
		transition: color .25s ease;
	}
	&:hover &__title {
		color: var(--front-brand);
	}
	&__descr {
		color: var(--front-primary_50p);
		@include style_1\2_1\25(400);
	}
}
