// В этом файле должны быть стили для БЭМ-блока form, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.form {
	&__inner {
		display: grid;
		gap: 1rem;
		@media screen and (min-width: $screen-t) {
			gap: 2rem;
		}
		:is(.registration) & {
			gap: 3rem;
		}
		:is(.lk) & {
			gap: 2rem;
		}
	}
	&__field {
		display: grid;
		gap: 1rem;
	}
	&__label {
		color: inherit;
		@include style_1\5_1\33;
		&[data-required]::after {
			color: var(--front-alarm);
			content: '*';
			font-size: 1.5rem;
			font-weight: 700;
			line-height: 1rem;
			padding-left: .3rem;
		}
	}
	&__input,
	&__textarea {
		display: block;
		width: 100%;
	}
	&__submit {
		line-height: 1.3334;
		margin-top: 1rem;
		padding: 1.4rem .9rem;
	}
	&__small {
		@include form-small;
		display: block;
		margin-top: 3rem;
		& + & {
			margin-top: 1.5rem;
		}
	}
	:is(.registration) &__input-file {
		padding: 1.2rem 1.9rem;
	}

	&__field-info {
		background: var(--back-tertiary);
		padding: 1rem;
	}
	&__field-info-p {
		color: var(--front-primary_50p);
		@include style_1\5_1;
		margin: 0 0 1rem;
	}
	&__field-info-list {
		display: grid;
		gap: .5rem;
		margin: 0;
		padding: 0 0 0 2rem;
	}
	&__field-info-li {
		@include style_1\5_1;
		color: var(--front-primary_50p);
	}
	&__file {
		align-items: center;
		border: .1rem solid var(--border-brand_50p);
		display: flex;
		gap: 1rem;
		justify-content: space-between;
		padding: 1rem;
	}
	&__file-img {
		height: 10rem;
		width: 10rem;
	}
	&__file-remove {
		cursor: pointer;
	}
	&__file-close {
		flex: 0 0 auto;
		height: 3.5rem;
		width: 3.5rem;
	}
}
