.air-datepicker {
	--adp-font-family: 'Berlin Type', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Helvetica Neue', Arial, sans-serif;
	--adp-font-size: 15px;
	--adp-width: 330px;
	--adp-z-index: 100;
	--adp-padding: 10px;
	--adp-grid-areas: 'nav' 'body' 'timepicker' 'buttons';
	--adp-transition-duration: .3s;
	--adp-transition-ease: ease-out;
	--adp-transition-offset: 10px;
	--adp-background-color: #fff;
	--adp-background-color-hover: #f0f7ff;
	--adp-background-color-active: #eaeaea;
	--adp-background-color-in-range: rgba(92, 196, 239, .1);
	--adp-background-color-in-range-focused: rgba(92, 196, 239, .2);
	--adp-background-color-selected-other-month-focused: #8ad5f4;
	--adp-background-color-selected-other-month: #a2ddf6;
	--adp-color: #4a4a4a;
	--adp-color-secondary: #9c9c9c;
	--adp-accent-color: #0069b4;
	--adp-color-current-date: var(--adp-accent-color);
	--adp-color-other-month: #dedede;
	--adp-color-disabled: #aeaeae;
	--adp-color-disabled-in-range: #939393;
	--adp-color-other-month-hover: #f0f7ff;
	--adp-border-color: #dbdbdb;
	--adp-border-color-inner: #efefef;
	--adp-border-radius: 0;
	--adp-border-color-inline: #d7d7d7;
	--adp-nav-height: 32px;
	--adp-nav-arrow-color: #0069b4;
	--adp-nav-action-size: 32px;
	--adp-nav-color-secondary: var(--adp-color-secondary);
	--adp-day-name-color: #0069b4;
	--adp-day-name-color-hover: #8ad5f4;
	--adp-day-cell-width: 1fr;
	--adp-day-cell-height: 32px;
	--adp-month-cell-height: 42px;
	--adp-year-cell-height: 56px;
	--adp-pointer-size: 10px;
	--adp-poiner-border-radius: 0;
	--adp-pointer-offset: 14px;
	--adp-cell-border-radius: 0;
	--adp-cell-background-color-hover: var(--adp-background-color-hover);
	--adp-cell-background-color-selected: #0069b4;
	--adp-cell-background-color-selected-hover: #005999;
	--adp-cell-background-color-in-range: rgba(92, 196, 239, 0.1);
	--adp-cell-background-color-in-range-hover: rgba(92, 196, 239, 0.2);
	--adp-cell-border-color-in-range: var(--adp-cell-background-color-selected);
	--adp-btn-height: 35px;
	--adp-btn-color: var(--adp-accent-color);
	--adp-btn-color-hover: var(--adp-color);
	--adp-btn-border-radius: var(--adp-border-radius);
	--adp-btn-background-color-hover: var(--adp-background-color-hover);
	--adp-btn-background-color-active: var(--adp-background-color-active);
	--adp-time-track-height: 1px;
	--adp-time-track-color: #dedede;
	--adp-time-track-color-hover: #b1b1b1;
	--adp-time-thumb-size: 12px;
	--adp-time-padding-inner: 10px;
	--adp-time-day-period-color: var(--adp-color-secondary);
	--adp-mobile-font-size: 16px;
	--adp-mobile-nav-height: 40px;
	--adp-mobile-width: 320px;
	--adp-mobile-day-cell-height: 38px;
	--adp-mobile-month-cell-height: 48px;
	--adp-mobile-year-cell-height: 64px;

	.air-datepicker-body--day-name {
		@include adp-week(700);
    }
	.air-datepicker-nav--title {
		@include adp-title(700);
		small {
			@include adp-title(700);
		}
	}
	&.-inline- {
		border: none;
		width: 100%;
		.air-datepicker-nav,
		.air-datepicker--content {
			padding: 0;
		}
		.air-datepicker-nav {
			border: none;
			padding-bottom: 1rem;
		}
	}
	.air-datepicker-body--day-name,
	.air-datepicker-cell.-day- {
		aspect-ratio: 1/1;
	}
	.air-datepicker-body--cells.-days- {
		grid-auto-rows: auto;
	}
	.air-datepicker-body--day-names {
		margin-top: 0;
	}
	.air-datepicker--content {
		padding-top: 0;
	}
	.air-datepicker-cell {
		position: relative;
	}
	.air-datepicker-cell.-current- {
		font-weight: 700;
	}
	.air-datepicker-buttons {
		display: grid;
		gap: 1rem 2rem;
		grid-template-columns: 1fr 1fr;
		> *:nth-child(2) {
			grid-column: 1;
		}
		> *:nth-child(3) {
			grid-column: 2;
		}
		> *:first-child,
		> *:last-child {
			grid-column: 1 / span 2;
		}
	}
	.air-datepicker-button {
		background: var(--back-tertiary);
		@include button-title;
		text-transform: uppercase;
	}
}

.air-datepicker-events {
	bottom: 0;
	display: flex;
	justify-content: center;
	gap: .2rem;
	left: 0;
	position: absolute;
	width: 100%;
}
.air-datepicker-event {
	border-radius: 50%;
	height: .5rem;
	width: .5rem;
	&_international {
		background: var(--front-warning);
	}
	&_local {
		background: var(--front-alarm);
	}
	&_interregional {
		background: var(--front-yellow);
	}
	&_regional {
		background: var(--front-violet);
	}
	&_intraschool {
		background: var(--front-cyan);
	}
	&_internationalDay {
		border: .15rem solid var(--front-brand);
	}
}
