.pagination {
	align-items: center;
	display: flex;
	flex-direction: column;
	gap: 1.5rem;
	@media screen and (min-width: $screen-ti) {
		flex-direction: row;
		justify-content: space-between;
	}
	&__pages {
		display: flex;
		gap: 1rem;
		@media screen and (min-width: $screen-t) {
			gap: 1.5rem;
		}
	}
	&__current,
	&__link,
	&__span {
		align-items: center;
		display: flex;
		@include style_1\2_1\25(400);
		justify-content: center;
		min-height: 3.5rem;
		min-width: 3.5rem;
		padding: 0 .5rem;
		text-decoration: none;
		@media screen and (min-width: $screen-t) {
			@include style_1\5_1;
			min-height: 4.5rem;
			min-width: 4.5rem;
		}
	}
	&__current {
		background: var(--back-brand);
		color: var(--front-invert);
		cursor: default;
	}
	&__link {
		border: .1rem solid var(--border-brand-light);
		color: inherit;
		transition: background .25s ease;
		&:hover {
			background: var(--back-secondary);
		}
	}
	&__span {
		cursor: default;
	}
	&__field {
		align-items: center;
		display: flex;
		gap: 2.5rem;
	}
	&__descr {
		color: var(--front-primary_75p);
		@include inner-grid-descr;
	}
	&__input {
		height: auto;
		line-height: unset;
		padding: .9rem 1.4rem;
		text-align: center;
		width: 13rem;
		@media screen and (min-width: $screen-t) {
			padding: 1.4rem 1.9rem;
		}
	}
}
