// В этом файле должны быть стили для БЭМ-блока experience, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.experience {
	&__title {
		@include experience;
		margin: 0;
		span {
			color: var(--front-brand);
			font-weight: 700;
		}
	}
	&__journal {
		display: grid;
		gap: 1rem;
		margin-top: 3rem;
		@media screen and (min-width: $screen-t) {
			gap: 2rem;
		}
		@media screen and (min-width: $screen-ti) {
			gap: 2rem;
			grid-template-columns: 1fr 1fr;
		}
	}
	&__journal-item {
		border: .1rem solid var(--border-dark_50p);
		color: var(--front-brand);
		display: flex;
		flex-direction: column;
		gap: 1rem;
		padding: .9rem;
		position: relative;
		text-decoration: none;
		transition: border .25s ease;
		@media screen and (min-width: $screen-t) {
			padding: 7.5rem 1.9rem 1.9rem;
		}
		&[href] {
			border: .1rem solid var(--border-brand-light);
			&:hover {
				border-color: var(--border-brand);
			}
		}
	}
	&__journal-icon {
		height: 4rem;
		width: 4rem;
		@media screen and (min-width: $screen-t) {
			left: 1.5rem;
			position: absolute;
			top: 1.5rem;
		}
	}
	&__journal-title {
		color: inherit;
		@include journal-title;
	}
	&__journal-descr {
		color: var(--front-primary_75p);
		@include person-descr;
	}
	&__links {
		display: grid;
		gap: 1rem;
		@media screen and (min-width: $screen-t) {
			gap: 2rem;
		}
	}
	&__links-item {
		border: .1rem solid var(--border-dark_50p);
		color: var(--front-brand);
		display: flex;
		flex-direction: column;
		gap: 1rem;
		padding: .9rem;
		text-decoration: none;
		transition: border .25s ease;
		@media screen and (min-width: $screen-t) {
			padding: 1.9rem;
		}
		&[href] {
			border: .1rem solid var(--border-brand-light);
			&:hover {
				border-color: var(--border-brand);
			}
		}
	}
	&__links-title {
		color: inherit;
		@include journal-title;
	}
	&__links-item[href^='http'] &__links-title::after {
		background: url('../img/svgSprite.svg#icon__external') no-repeat;
		background-size: 100% 100%;
		content: '';
		display: inline-block;
		height: 2.5rem;
		margin-left: .5rem;
		width: 2.5rem;
	}
	&__links-descr {
		color: var(--front-primary_75p);
		@include person-descr;
	}

	.paragraph {
		margin-top: 2rem;
		max-width: 65.5rem;
		@media screen and (min-width: $screen-d) {
			margin-top: 4rem;
		}
	}
}
