// В этом файле должны быть стили для БЭМ-блока entrance, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.entrance {
	&.section:only-of-type {
		@media screen and (min-width: $screen-ti) {
			padding: 5rem 0 10rem;
		}
		@media screen and (min-width: $screen-d) {
			padding: 7rem 0 15rem;
		}
	}
	&__inner {
		display: flex;
		flex-direction: column;
		margin: 0 auto;
		max-width: 38.5rem;
		position: relative;
		@media screen and (min-width: $screen-tl) {
			margin: 0;
		}
	}
	&__title {
		font-size: 2.5rem;
		font-weight: 700;
		line-height: 1;
		margin: 0 0 2.5rem;
	}
	&__link {
		align-self: center;
		color: var(--front-brand);
		@include style_1\5_1\33;
		margin-top: 3rem;
		text-decoration: none;
		@media screen and (min-width: $screen-tl) {
			align-self: flex-start;
		}
		&:hover {
			text-decoration: underline;
		}
	}
	&__descr {
		@include style_1\5_1\33;
		& + & {
			margin-top: 1.5rem;
		}
	}
	&__descr + .form {
		margin-top: 3.5rem;
	}
	&__tiles {
		margin-top: 2rem;
		@media screen and (min-width: $screen-t) {
			margin-top: 5rem;
		}
	}
}
