// В этом файле должны быть стили для БЭМ-блока page, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.page {
	display: flex;
	flex-direction: column;
	//height: 100%;
	min-height: 100%;
	overflow: hidden;
	position: relative;
	.glightbox-open &,
	[data-state='nav_open'] & {
		width: calc(100vw - var(--scrollbar-width));
	}
	&__header {
		flex: 0 0 auto;
		left: 0;
		position: fixed;
		right: 0;
		top: 0;
		z-index: 5;
		.glightbox-open &,
		.hystmodal__opened &,
		[data-state='nav_open'] & {
			width: calc(100vw - var(--scrollbar-width));
		}
	}
	&__main {
		flex: 1 0 auto;
		padding-top: 16rem;
		position: relative;
		z-index: 1;
		@media screen and (min-width: $screen-tl) {
			padding-top: 18rem;
		}
		:is(.page) &_no-pt {
			padding-top: 0;
		}
	}
	&__footer {
		flex: 0 0 auto;
		position: relative;
		z-index: 2;
	}
	&__nav {
		height: 100%;
		left: 0;
		opacity: 0;
		pointer-events: none;
		position: fixed;
		right: 0;
		top: 0;
		transform: translateY(5rem) scale(.9);
		transition: opacity .25s ease-in-out, transform .25s ease;
		width: calc(100vw - var(--scrollbar-width));
		will-change: opacity, transform;
		z-index: 4;
		@media screen and (min-width: $screen-t) {
			transform: translateY(-2.5rem) scale(1);
		}
		[data-state='nav_open'] & {
			pointer-events: auto;
			opacity: 1;
			transform: translateY(0) scale(1);
		}
	}
	&__darker {
		backdrop-filter: blur(.4rem);
		background: var(--back-darker);
		display: block;
		height: 100%;
		left: 0;
		opacity: 0;
		position: fixed;
		top: 0;
		transition: all .25s;
		visibility: hidden;
		width: calc(100vw - var(--scrollbar-width));
		z-index: 3;
		@media screen and (min-width: $screen-d) {
			display: none;
		}
		[data-state='nav_open'] & {
			opacity: 1;
			transition: opacity .25s;
			visibility: visible;
		}
	}
	&__to-top-button {
		backdrop-filter: blur(.8rem);
		background: var(--back-brand_75p);
		bottom: 1.5rem;
		height: 4.5rem;
		opacity: 0;
		outline: .1rem solid var(--border-light_50p);
		outline-offset: -.1rem;
		padding: 1rem;
		position: fixed;
		right: 1.5rem;
		transform: translateY(10rem);
		transition: opacity .25s ease-out, outline .25s ease-out, outline-offset .25s ease-out, transform 0.5s ease;
		width: 4.5rem;
		z-index: 2;
		@media screen and (min-width: $screen-t) {
			bottom: 2.5rem;
			right: 4rem;
			.hystmodal__opened &,
			[data-state='nav_open'] & {
				right: calc(4rem + var(--scrollbar-width));
			}
		}
		@media screen and (min-width: $screen-d) {
			bottom: 4rem;
			right: 4rem;
			.hystmodal__opened &,
			[data-state='nav_open'] & {
				right: calc(4rem + var(--scrollbar-width));
			}
		}
		@media screen and (min-width: $screen-dxl) {
			bottom: 4rem;
			left: calc(50% + 75.5rem);
			right: auto;
			.hystmodal__opened &,
			[data-state='nav_open'] & {
				left: calc(50% + 75.5rem - (var(--scrollbar-width) / 2));
				right: auto;
			}
		}
		&:hover {
			cursor: pointer;
			outline: .1rem solid var(--border-light);
		}
		&:active {
			outline-offset: -.5rem;
		}
		&_active {
			opacity: 1;
			transform: translateY(0);
		}
	}
	&__icon {
		height: 2.5rem;
		stroke: var(--front-invert);
		width: 2.5rem;
	}
}

.headroom {
	will-change: transform;
	transition: transform .25s linear;
	&--pinned {
		transform: translateY(0%);
	}
	&--unpinned {
		transform: translateY(-100%);
	}
}
