// В этом файле должны быть стили для БЭМ-блока selectizer, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.selectizer {
	display: flex;
	flex-wrap: wrap;
	gap: 1rem;
	padding-top: .5rem;
	&__item {}
	&__radio,
	&__checkbox {
		display: none;
	}
	&__button {
		background: var(--back-tertiary);
		color: inherit;
		cursor: pointer;
		font-size: 1.5rem;
		font-weight: 700;
		line-height: 1;
		padding: 1rem 1.5rem;
		transition: background .25s ease, color .25s ease;
	}
	&__radio:checked + &__button,
	&__checkbox:checked + &__button {
		background: var(--front-brand);
		color: var(--front-invert);
	}
}
