// В этом файле должны быть стили для БЭМ-блока buttons-set, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.buttons-set {
	display: flex;
	& > * {
		flex: 1 0 auto;
		position: relative;
		&:hover {
			z-index: 2;
		}
	}
	& > * + * {
		margin-left: -.1rem;
	}
}
