// В этом файле должны быть стили для БЭМ-блока button, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.button {
	align-items: center;
	appearance: none;
	background: none;
	border: .1rem solid var(--border-dark);
	color: var(--front-brand);
	cursor: pointer;
	display: flex;
	gap: 1rem;
	justify-content: center;
	line-height: 1.25;
	padding: .9rem;
	stroke: var(--front-brand);
	text-decoration: none;
	transition: background .25s ease, border .25s ease;
	&:hover {
		border: .1rem solid var(--border-brand);
	}
	&_active {
		border: .1rem solid var(--border-brand);
		cursor: default;
		z-index: 3;
		&.button_invert {
			border: .1rem solid var(--border-light);
		}
	}
	&_invert {
		border: .1rem solid var(--border-light_50p);
		color: var(--front-invert);
		stroke: var(--front-invert);
		&:hover {
			border: .1rem solid var(--border-light);
		}
	}
	&_solid {
		background: var(--border-brand);
		border: .1rem solid var(--border-brand);
		color: var(--front-invert);
		stroke: var(--front-invert);
		&:hover {
			background: var(--back-brand-dark);
		}
	}
	&_solid-invert {
		background: var(--border-brand);
		border: .1rem solid var(--border-light_50p);
		color: var(--front-invert);
		stroke: var(--front-invert);
		&:hover {
			background: var(--back-brand-dark);
		}
	}
	&_reset {
		color: var(--front-primary_75p);
	}
	&_large {
		line-height: 1;
	}
	&__title {
		@include button-title;
		text-transform: uppercase;
		transform: translateY(.1rem);
	}
	&_large &__title {
		@include button-title-large;
		text-transform: uppercase;
	}
	&__icon {
		stroke: inherit;
		height: 1.5rem;
		width: 1.5rem;
	}
	&_iconized &__icon {
		@media screen and (min-width: $screen-tl) {
			height: 2.5rem;
			width: 2.5rem;
		}
	}
}
