.lead {
	color: var(--front-primary_75p);
	@include lead;
	margin: 0;
}
.paragraph {
	@include paragraph;
	margin: 0;
}
.list {
	list-style: none;
	margin: 0;
	padding: 0;
	li {
		color: var(--front-primary_75p);
		@include list-item;
		padding-left: 2rem;
		position: relative;
		@at-root {
			ul#{&}::before {
				background: var(--front-brand);
				top: 0;
				content: '';
				display: block;
				height: .5rem;
				left: 0;
				position: absolute;
				transform: translateY(.7rem);
				width: .5rem;
			}
		}
	}
	li + li {
		margin-top: 1rem;
	}
}
