// В этом файле должны быть стили для БЭМ-блока popup, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.popup {
	max-width: 63rem;
	width: calc(100% - 3rem);
	&__inner {
		padding: 2rem;
		@media screen and (min-width: $screen-t) {
			padding: 4.5rem 10.5rem;
		}
	}
	&__title {
		@include popup-title;
		margin: 0 0 2rem;
		padding-right: 4rem;
	}
	&__descr {
		@include style_1\5_1\33;
		color: var(--front-primary_75p);
		margin: 0;
	}
	&__close {
		outline: .1rem solid transparent;
		right: 2rem;
		top: 2rem;
		transition: outline .25s ease-in-out, outline-offset .25s ease-in-out;
		&:hover {
			outline: .1rem solid var(--border-dark);
		}
		&:active {
			outline-offset: -.5rem;
		}
	}
	&__icon {
		fill: var(--front-primary);
		height: 3.5rem;
		width: 3.5rem;
	}
	&__form {
		margin: 3rem 0 0;
	}
}
