// В этом файле должны быть стили для БЭМ-блока join, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.join {
	&__list {
		align-items: flex-start;
		display: flex;
		flex-direction: column;
		gap: 1.5rem;
		list-style: none;
		margin: 0;
		padding: 0;
		counter-reset: item;
		&[start='4'] {
			counter-reset: item 3;
		}
		&[start='6'] {
			counter-reset: item 5;
		}
		&[start='8'] {
			counter-reset: item 7;
		}
	}
	&__item {
		counter-increment: item;
		@include join-list-item;
		padding: .5rem 1rem .5rem 2.5rem;
		position: relative;
		transform: translate(-.5rem, -.5rem);
		&_mark {
			background: var(--back-tertiary);
			@include join-list-item(700);
		}
		&::before {
			content: counter(item)". ";
			left: 1rem;
			position: absolute;
		}
	}
	&__documents {
		align-items: start;
		border: .1rem solid var(--border-brand-light);
		display: flex;
		flex-direction: column;
		gap: 1.5rem;
		margin-top: 1rem;
		padding: 1.4rem;
	}

	.lead,
	.paragraph {
		color: var(--front-primary_75p);
		max-width: 65.5rem;
	}
}
