// В этом файле должны быть стили для БЭМ-блока article, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.article {
	&__inner {
		display: grid;
		gap: 3.5rem;
		@media screen and (min-width: $screen-ti) {
			grid-template-areas:
					'content content'
					'info side';
			grid-template-columns: 1fr 1fr;
		}
		@media screen and (min-width: $screen-tl) {
			align-content: start;
			align-items: start;
			gap: 3.5rem 2rem;
			grid-template-areas: none;
			grid-template-columns: repeat(12, 1fr);
		}
		@media screen and (min-width: $screen-d) {
			gap: 2rem;
		}
	}
	&__wrapper {
		display: grid;
		gap: 3.5rem;
		@media screen and (min-width: $screen-ti) {
			grid-area: content;
		}
		@media screen and (min-width: $screen-tl) {
			grid-column: 4 / span 9;
			grid-row: 1;
		}
		@media screen and (min-width: $screen-d) {
			grid-column: 4 / span 6;
			grid-row: 1;
		}
	}
	&__info {
		align-content: start;
		display: grid;
		gap: 2rem;
		@media screen and (min-width: $screen-ti) {
			grid-area: info;
		}
		@media screen and (min-width: $screen-tl) {
			gap: 4rem;
			grid-column: 1 / span 3;
			grid-row: 1;
			max-width: 28rem;
		}
		@media screen and (min-width: $screen-d) {
			grid-column: 1 / span 3;
			grid-row: 1;
		}
	}
	&__side {
		display: grid;
		gap: 2rem;
		@media screen and (min-width: $screen-ti) {
			grid-area: side;
		}
		@media screen and (min-width: $screen-tl) {
			grid-column: 4 / span 9;
			grid-row: 2;
			max-width: 65.5rem;
		}
		@media screen and (min-width: $screen-d) {
			grid-column: 10 / span 3;
			grid-row: 1;
		}
	}
	&__header {}
	&__title {
		@include article-h1;
		margin: 0 0 1.5rem;
		position: relative;
		@media screen and (min-width: $screen-tl) {
			margin: 0 0 2.5rem;
		}
	}
	&__tags {
		margin-bottom: 3.5rem;
		.tags__item {
			background: transparent;
		}
	}
	&__lead {
		max-width: 65.5rem;
	}
	&__label {
		color: var(--front-brand);
		@include section-subtitle;
		text-decoration: none;
		text-transform: uppercase;
	}
	&__subtitle {
		color: var(--front-primary_50p);
		@include grid-subtitle;
		margin-bottom: 1.5rem;
		@media screen and (min-width: $screen-tl) {
			margin: 0;
		}
	}
	&__date {
		color: var(--front-primary_75p);
		@include style_1\5_1;
	}
	&__slider {
		max-width: 65.5rem;
		margin: 0;
		padding-bottom: 7rem;
		position: relative;
		width: 100%;
		@media screen and (min-width: $screen-tl) {
			padding-bottom: 4.5rem;
		}
		.touch & {
			padding-bottom: 1rem !important;
		}
	}
	&__slider-controls {
		bottom: 0;
		display: grid;
		gap: 2.3rem;
		justify-items: start;
		position: absolute;
		width: 100%;
		@media screen and (min-width: $screen-tl) {
			align-items: center;
			display: flex;
			gap: 2rem;
			justify-content: space-between;
		}
	}
	&__slide {}
	&__slide-image {
		margin: 0;
	}
	&__slide-link {
		display: block;
		overflow: hidden;
		position: relative;
		&::before {
			background: var(--back-darker);
			content: '';
			display: block;
			height: 100%;
			left: 0;
			opacity: 0;
			pointer-events: none;
			position: absolute;
			top: 0;
			transition: opacity 0.4s;
			width: 100%;
			z-index: 99;
		}
		&:hover::before {
			opacity: 0.3;
			transition: opacity 0.5s;
		}
	}
	&__slide-img {
		aspect-ratio: 16/9;
		background: var(--back-tertiary);
		display: block;
		object-fit: scale-down;
		transition: transform .25s ease;
		width: 100%;
		&:hover {
			transform: scale(1.05);
		}
	}
	&__slide-row {
		display: grid;
		gap: .5rem;
		padding: .5rem 0;
		@media screen and (min-width: $screen-ti) {
			display: grid;
			gap: 2rem;
			grid-template-columns: auto max(30%);
		}
	}
	&__slide-caption {
		color: var(--front-primary_75p);
		@include style_1\2_1\25(400);
	}
	&__slide-author {
		color: var(--front-primary_75p);
		@include style_1\2_1\25(400);
		@media screen and (min-width: $screen-ti) {
			text-align: right;
		}
		&::before {
			content: 'Фото: ';
		}
	}
	&__slider-pagination {
		display: flex;
		gap: .5rem;
		.touch & {
			justify-content: center;
			width: 100%;
		}
		.swiper-pagination-bullet {
			background: var(--back-brand);
			border-radius: .2rem;
			opacity: .5;
			height: .2rem;
			transition: opacity .25s ease;
			width: 2rem;
		}
		.swiper-pagination-bullet-active {
			opacity: 1;
		}
	}
	:is(.article) & {
		&__slider-disabled {
			filter: grayscale(1);
			opacity: .5;
			pointer-events: none;
		}
		&__slider-prev,
		&__slider-next {
			padding: .4rem;
		}
	}
	.touch & .buttons-set {
		display: none;
	}
	&__slider-icon {
		height: 2.5rem;
		width: 2.5rem;
	}
	&__wysiwyg {
		max-width: 65.5rem;
	}
	&__additions {
		display: grid;
		gap: 3rem;
		max-width: 65.5rem;
	}
}
