// В этом файле должны быть стили для БЭМ-блока nav, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.nav {
	background: var(--back-brand);
	color: var(--front-invert);
	height: 100%;
	overflow: hidden;
	width: 100%;
	@media screen and (min-width: $screen-ti) {
		height: auto;
	}
	@media screen and (min-width: $screen-d) {
		display: none;
	}
	.grid {
		height: 100%;
		@media screen and (min-width: $screen-ti) {
			height: auto;
		}
	}
	&__inner {
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		height: 100%;
		overflow: hidden;
-webkit-overflow-scrolling: touch;
		overflow-y: auto;
		padding: 2rem 0;
		@media screen and (min-width: $screen-t) {
			display: grid;
			grid-template-areas:
				'. nav .'
				'. actions .';
			grid-template-columns: 4fr 8fr;
			margin: 0 -1rem;
			padding: 18rem 0 4rem;
		}
		@media screen and (min-width: $screen-ti) {
			grid-template-columns: 3fr 7fr 2fr;
			padding: 20rem 0 4rem;
		}
	}
	&__nav {
		margin-top: max(15rem, 23vh);
		@media screen and (min-width: $screen-t) {
			grid-area: nav;
			margin: 0;
			padding: 0 1rem;
		}
		@media screen and (min-width: $screen-ti) {
			display: none;
		}
	}
	&__list {
		display: grid;
		gap: 1.5rem;
		list-style: none;
		margin: 0;
		padding: 0;
		@media screen and (min-width: $screen-t) {
			gap: 2.5rem;
		}
	}
	&__item {
		display: flex;
	}
	&__link {
		color: inherit;
		@include style_1\8_1\12(400);
		text-decoration: none;
		transition: opacity .25s ease;
		white-space: nowrap;
		&:hover {
			opacity: .75;
		}
	}
	&__block {
		@media screen and (min-width: $screen-t) {
			align-self: end;
			grid-area: actions;
			margin: 0;
			padding: 0 1rem;
		}
	}
	&__actions {
		display: flex;
		flex-direction: column;
		gap: 1rem;
		@media screen and (min-width: $screen-t) {
			gap: 2rem;
			width: 34rem;
		}
	}
	&__row {
		display: flex;
		gap: 1rem;
		@media screen and (min-width: $screen-t) {
			gap: 2rem;
		}
		& > * {
			flex: 1 0 auto;
		}
		&:nth-child(2) > *:nth-child(1){
			//flex: 0 0 auto;
		}
	}
}
