// В этом файле должны быть стили для БЭМ-блока people, его элементов,
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...
// Очередность: http://nicothin.github.io/idiomatic-pre-CSS/#priority

.inner-grid {
	&__section {
		position: relative;
		width: 100%;
		@media screen and (min-width: $screen-d) {
			display: flex;
			margin: 0 -1rem;
			width: calc(100% + 2rem);
		}
	}
	&_form &__section {
		@media screen and (min-width: $screen-ti) {
			display: flex;
		}
	}
	&_people &__section {
		padding: 3.5rem 0;
		@media screen and (min-width: $screen-d) {
			padding: 5rem 0;
		}
		&:first-child {
			padding-top: 0;
		}
		&:last-child {
			padding-bottom: 0;
		}
		+ .inner-grid__section {
			border-top: .2rem dashed var(--border-brand-light);
			&_school {
				border-top: none;
			}
		}
		&_school {
			@media screen and (min-width: $screen-d) {
				padding-bottom: 2rem;
				&:not(:first-child) {
					padding-bottom: 5rem;
				}
				& + & {
					padding-top: 0;
				}
			}
			+ .inner-grid__section_school {
				@media screen and (min-width: $screen-d) {
					padding-top: 0;
				}
			}
		}
	}
	&_instructions &__section:not(&__section:first-child)::before {
		border: .2rem solid var(--border-brand_50p);
		border-left: 0;
		border-top: 0;
		content: '';
		display: block;
		height: 1rem;
		position: absolute;
		transform: translate(1.9rem, -1.9rem) rotate(45deg);
		width: 1rem;
		@media screen and (min-width: $screen-d) {
			content: none;
		}
	}
	&_instructions &__section:not(&__section:last-child)::after {
		border-right: .2rem solid var(--border-brand_50p);
		content: '';
		display: block;
		flex: 0 1 auto;
		height: 2.5rem;
		position: absolute;
		width: 2.5rem;
		@media screen and (min-width: $screen-t) {
			height: 4rem;
		}
		@media screen and (min-width: $screen-tl) {
			height: 6.5rem;
		}
		@media screen and (min-width: $screen-d) {
			content: none;
		}
	}
	&_instructions &__section + &__section {
		margin-top: 3.5rem;
		@media screen and (min-width: $screen-t) {
			margin-top: 5rem;
		}
		@media screen and (min-width: $screen-tl) {
			margin-top: 7.5rem;
		}
	}
	&__col {
		&:nth-child(1) {
			display: flex;
			flex-direction: column;
		}
		&:nth-child(2) {
			display: grid;
			gap: 1rem;
		}
		@media screen and (min-width: $screen-t) {
			&:nth-child(2) {
				gap: 2rem;
			}
		}
		@media screen and (min-width: $screen-ti) {
			.inner-grid__section_form & {
				padding: 0 1rem;
				&:nth-child(1) {
					width: 37.5%;
				}
				&:nth-child(2) {
					width: 62.5%;
				}
			}
		}
		@media screen and (min-width: $screen-d) {
			padding: 0 1rem;
			&:nth-child(1) {
				margin-right: 12.5%;
				width: 25%;
				.inner-grid__section_form & {
					margin-right: 0;
				}
			}
			&:nth-child(2) {
				width: 62.5%;
			}
		}
	}
	&__title {
		@include inner-grid-title;
		margin: 0 0 2rem;
		@media screen and (min-width: $screen-d) {
			margin: 0 0 1rem;
			.inner-grid__section_form & {
				max-width: 32rem;
			}
		}
	}
	&__descr {
		color: var(--front-primary_75p);
		@include inner-grid-descr;
		margin: 0;
	}
	&_instructions &__descr {
		transform: translateY(-1.5rem);
		@media screen and (min-width: $screen-d) {
			transform: none;
		}
	}
	&__arrow-block {
		display: none;
		flex: 1 1 auto;
		margin-top: 1.5rem;
		position: relative;
		width: 5rem;
		@media screen and (min-width: $screen-d) {
			display: block;
		}
		&::before {
			border-right: .2rem solid var(--border-brand_50p);
			content: '';
			display: block;
			flex: 0 1 auto;
			height: calc(100% + 6rem);
			position: relative;
			width: 2.5rem;
		}
		&::after {
			border: .2rem solid var(--border-brand_50p);
			border-left: 0;
			border-top: 0;
			content: '';
			display: block;
			height: 1rem;
			position: relative;
			transform: translate(1.9rem, -1rem) rotate(45deg);
			width: 1rem;
		}
	}
	&__section:last-child &__arrow-block {
		display: none;
	}
}
